<template>
    <div class="publicAllContentBg" id="artistScrollList">
        <!--标题-->
        <div class="publicContentHeaderBox">
            <div class="left"><van-icon :name="require('../assets/icon_stars_nor.png')"></van-icon>知名演员</div>
            <div class="right" @click="$router.push({name:'actorList'})">
                查看更多
            </div>
        </div>
        <!--底部列表-->
        <ul class="bottomList">
            <li v-for="(item,index) in singerList" :key="index">
                <div class="indexIconNavListStyle">
                    <div class="icon" @click="goArtistContent(item)">
                        <aesImage :primordial-img="true" :file-url="item.headIco"></aesImage>
                        <!--<img :src="item.headIco"/>-->
                        <!--                        <img :src="require('../../assets/indexIcon.png')"/>-->
                    </div>
                    <p class="text" @click="goArtistContent(item)">{{item.name}}</p>
                </div>
            </li>
        </ul>
        <!--视频列表-->
        <!--<van-swipe :loop="false" :width="88" :show-indicators="false">
            <van-swipe-item v-for="(item,index) in singerList" :key="index">
                <div class="indexIconNavListStyle">
                    <div class="icon" @click="goArtistContent(item)">
                        <aesImage :primordial-img="true" :file-url="item.headIco"></aesImage>
                        &lt;!&ndash;<img :src="item.headIco"/>&ndash;&gt;
                        &lt;!&ndash;                        <img :src="require('../../assets/indexIcon.png')"/>&ndash;&gt;
                    </div>
                    <p class="text" @click="goArtistContent(item)">{{item.name}}</p>
                </div>
            </van-swipe-item>
        </van-swipe>-->
    </div>
</template>

<script>
import aesImage from "./aesImage";
export default {
    name: "artistScrollList",
    components:{
        aesImage
    },
    props:{
        singerList:Array
    },
    data(){
        return {

        }
    },
    methods:{
        /*艺人首页*/
        goArtistContent(item) {
            this.$router.push({name: "actorContent", params: {id: item.id}});
        },
    },
    created() {
    }
}
</script>

<style scoped>
.bottomList{
    display: flex;
}
.bottomList>li{
    width: 20%;
}
.indexIconNavListStyle {
    width: 100%;
    height: auto;
    overflow: hidden;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
}

.indexIconNavListStyle p.text {
    width: 100%;
    height: auto;
    overflow: hidden;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    text-align: center;
    color: #EAF1FF;
    font-size: 14px;
}

.indexIconNavListStyle div.icon {
    width: 80%;
    padding-top: 80%;
    border-radius: 50%;
    height: auto;
    overflow: hidden;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    margin: 0 auto 4px auto;
    position: relative;
}

.indexIconNavListStyle img {
    display: block;
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
}
</style>



