<template>
    <div class="publicAllContentBg allIndex fullHeight">
        <van-nav-bar
            :border="false"
            title="个人中心"
            class="publicAllNavbarClass"
        />
        <!--顶部公告-->
        <!--<div class="topBox">
            <div class="left">
                <van-notice-bar
                    left-icon="volume-o"
                    :scrollable="false"
                    background="transparent"
                    color="#EAF1FF"
                >
                    <van-swipe
                        vertical
                        class="notice-swipe"
                        :autoplay="3000"
                        :show-indicators="false"
                    >
                        <van-swipe-item v-for="(item,index) in signText" :key="index">{{
                                item.content
                            }}
                        </van-swipe-item>
                    </van-swipe>
                </van-notice-bar>
            </div>
            <div class="right" @click="logout" v-if="$store.state.isLogin">
                <van-icon :name="require('../../assets/icon_exit_nor.png')" color="#EAF1FF" size="20"/>
            </div>
        </div>-->
        <van-pull-refresh v-model="refreshLoadingStatus" @refresh="onRefresh">

            <div class="publicNeedXPaddingBg">
                <!--公告栏-->
                <van-cell class="userCenterIndexCell" :title="bottomSignContent.title" :label="bottomSignContent.content" />
                <!--用户信息显示-->
                <!--个人信息-->
                <div class="userInfoBox">
                    <!--钻石和vip-->
                    <ul class="middle">
                        <li>
                            <p>钻石(个)</p>
                            <h3>{{ returnPoint }}</h3>
                            <div class="button" @click="goRecharge">充值</div>
                        </li>
                        <li>
                            <p>VIP剩(天)</p>
                            <h3>{{ returnVipDays }}</h3>
                            <div class="button" @click="goRecharge">续期</div>
                        </li>
                    </ul>
                    <!--用户昵称-->
                    <div class="name" v-if="$store.state.isLogin">
                        <p>{{ returnUserName }}</p>
                        <!--<p class="date">{{ returnVipText }}</p>-->
                    </div>
                    <!--用户昵称-->
                    <div class="name unLogin" v-if="!$store.state.isLogin" @click="goLogin">请先登录</div>
                </div>
                <!--功能列表-->
                <ul class="functionList">
                    <li v-for="(item,index) in buttonOptions.list" :key="index">
                        <div class="functionListStyle" @click="userClickButton(index)">
                            <div class="icon">
                                <van-icon :name="item.icon" size="24" color="#A0A4AE"/>
                            </div>
                            <p>{{ item.name }}</p>
                        </div>
                    </li>
                    <!--退出登录和立即登录-->
                    <li v-if="$store.state.isLogin">
                        <div class="functionListStyle" @click="logout">
                            <div class="icon">
                                <van-icon :name="require('../../assets/icon_exit_nor.png')" size="24" color="#A0A4AE"/>
                            </div>
                            <p>登出账号</p>
                        </div>
                    </li>
                    <li v-if="!$store.state.isLogin">
                        <div class="functionListStyle" @click="goLogin">
                            <div class="icon">
                                <van-icon name="user-o" size="24" color="#A0A4AE"/>
                            </div>
                            <p>登录账号</p>
                        </div>
                    </li>
                </ul>

            </div>
        </van-pull-refresh>
    </div>
</template>

<script>
export default {
    name: "userCenterIndex",
    computed: {
        returnVipText() {
            if (!this.$store.state.isLogin) {
                return "登录查看";
            }
            let newDate = Date.parse(new Date()) + this.$store.state.userInfo.vipMinutes * 60 * 1000;
            return 'VIP ' + this.$globalFn.getFormatData(newDate);
        },
        /*返回剩余钻石*/
        returnPoint() {
            if (!this.$store.state.isLogin) {
                return "*"
            }
            return this.$store.state.userInfo.point;
        },
        /*返回vip剩余天数*/
        returnVipDays() {
            if (!this.$store.state.isLogin) {
                return '*';
            }
            return (this.$store.state.userInfo.vipMinutes / 60 / 24).toFixed(0);
        },
        /*返回用户昵称*/
        returnUserName() {
            if (!this.$store.state.isLogin) {
                return "请先登录";
            }
            if (this.$store.state.userInfo.nickname === null || this.$store.state.userInfo.nickname === "") {
                return this.$store.state.userInfo.userName;
            }
            return this.$store.state.userInfo.nickname
        },
    },
    data() {
        return {
            /*下拉刷新是否在loading*/
            refreshLoadingStatus: false,
            /*通知内容*/
            signText: [],
            /*底部公告内容*/
            bottomSignContent:{
                /*标题*/
                title:"读取中",
                /*内容*/
                content:"读取中"
            },
            /*按钮配置*/
            buttonOptions: {
                /*列表*/
                list: [
                    {
                        name: "修改密码",
                        icon: require("../../assets/userCenterTopIcon1.png")
                    },
                    {
                        name: "已解锁视频",
                        icon: require("../../assets/userCenterTopIcon2.png")
                    },
                    {
                        name: "我的收藏",
                        icon: require("../../assets/userCenterTopIcon3.png")
                    },
                    {
                        name: "常见问题",
                        icon: require("../../assets/userCenterTopIcon4.png")
                    },
                    {
                        name: "代理赚钱",
                        icon:'paid'
                    },
                    {
                        name: "在线客服",
                        icon: 'service-o'
                    },
                    {
                        name: "美女直播",
                        icon: 'smile-o'
                    }
                ]
            },
        }
    },
    methods: {
        /*去充值页面*/
        goRecharge(){
            if(!this.$store.state.isLogin){
                this.$router.push({
                    name:'login'
                });
                return;
            }
            this.$router.push({
                name:'rechargeIndex'
            });
        },
        /*获取用户信息*/
        getUserInfo() {
            /*获取信息*/
            this.$requestApi.getUserInfo({})
                .then(res => {
                    /*覆盖信息*/
                    this.$store.commit('userInfoSave', res.data);
                })
                .catch(err => {
                    this.$toast.fail(err.message);
                });
        },
        /*下拉刷新*/
        onRefresh() {
            this.signText=[];
            /*获取通知*/
            this.getNoticeFn();
            /*如果已经登录，请求以下*/
            if (this.$store.state.isLogin === true) {
                /*获取用户信息*/
                this.getUserInfo();
            }
            /*关闭loading*/
            this.refreshLoadingStatus = false;
        },
        /*按钮点击事件*/
        userClickButton(index) {
            switch (index) {
                case 0:
                    if(!this.$store.state.isLogin){
                        this.$toast.fail('请先登录');
                        return;
                    }
                    this.$router.push('login?type=2');
                    break;
                case 1:
                    if(!this.$store.state.isLogin){
                        this.$toast.fail('请先登录');
                        return;
                    }
                    this.$router.push({name: "myPaidVideo"});
                    break;
                case 2:
                    if(!this.$store.state.isLogin){
                        this.$toast.fail('请先登录');
                        return;
                    }
                    this.$router.push({name: "myCollection"});
                    break;
                case 3:
                    this.$router.push({name: "question"});
                    break;
                case 4:
                    /*代理赚钱*/
                    if (!this.$store.state.isLogin) {
                        this.$toast.fail('请先登录!');
                        this.$router.push('login');
                        return;
                    }

                    /*已经登录，拼接url跳转*/
                    this.$globalFn.publicOpenNewWindow(this.$globalFn.returnAgentLoginUrl());
                    break;
                case 5:
                    /*跳转客服*/
                    if (!this.$store.state.isLogin) {
                        this.$toast.fail('请先登录!');
                        this.$router.push('login');
                        return;
                    }
                    /*已经登录，拼接url跳转*/
                    this.$globalFn.publicOpenNewWindow(this.$globalFn.returnCustomerServiceUrl());
                    break;
                case 6:
                    /*跳转客服*/
                    this.$toast.fail('暂未开放、敬请期待！');
                    break;
            }
        },
        /*退出登录*/
        logout() {
            /*退出登录*/
            this.$dialog.confirm({
                title: '操作确认',
                message: '是否确认退出登录',
                className: "logoutPopup",
                overlay: false
            })
                .then(() => {
                    /*删除本地用户信息*/
                    this.$store.commit('changeLoginStatus', false);
                    this.$store.commit('userInfoSave', null);
                    /*清除本地缓存*/
                    localStorage.removeItem('videoMobileDarkStore');
                })
                .catch(() => {
                    // on cancel
                });
        },
        /*跳转登录*/
        goLogin() {
            this.$router.push({name: 'login'});
        },
        /*获取通知内容*/
        getNoticeFn() {
            this.signText = [];
            this.$requestApi.getSignText({})
                .then(res => {
                    this.signText = res.data.list.filter((el) => {
                        return el.type === 0;
                    });
                    this.bottomSignContent.title=res.data.list[0].title;
                    this.bottomSignContent.content=res.data.list[0].content;
                })
                .catch(err => {

                })
        },
    },
    created() {
        this.getNoticeFn();
    },
    beforeRouteLeave(to,from,next){
        this.$dialog.close();
        next();
    }
}
</script>

<style scoped>
/*功能按钮*/
.functionListStyle > p {
    color: #A0A4AE;
    font-size: 14px;
    text-align: center;
    margin-top: 4px;
}

.functionListStyle > div.icon {
    display: flex;
    justify-content: center;
}

.functionListStyle {
    width: 100%;
    height: auto;
    overflow: hidden;
    box-sizing: border-box;
}

.functionList {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    background-color: #1F242F;
    box-sizing: border-box;
    padding: 0 0 10px 0;
    border-radius: 5px;
    margin-top: 10px;
}

.functionList > li {
    width: 25%;
    margin-top: 14px;
}

.topBox {
    display: flex;
}

.topBox .left {
    width: 90%;
}

.topBox .right {
    width: 10%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.notice-swipe {
    height: 40px;
    line-height: 40px;
}

.userInfoBox > .middle > li {
    width: 50%;
    box-sizing: border-box;
    padding: 0 4px;
}

.userInfoBox > .middle {
    display: flex;
    justify-content: space-between;
    margin-top: -30px;
}

.userInfoBox > .middle .button {
    border: 1px solid #A0A4AE;
    border-radius: 5px;
    color: #A0A4AE;
    display: table;
    margin: auto;
    font-size: 14px;
    padding: 4px 6px;
}

.userInfoBox > .middle h3 {
    color: #F54B64;
    font-size: 24px;
    text-align: center;
    font-weight: 500;
    width: 100%;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
}

.userInfoBox > .middle p {
    color: #A0A4AE;
    font-size: 14px;
    text-align: center;
}

.userInfoBox > .name {
    text-align: center;
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    box-sizing: border-box;
    background-color: rgba(0, 0, 0, 0.2);
    font-size: 16px;
    padding: 14px 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.userInfoBox > .name.unLogin {
    justify-content: center;
    color: #A0A4AE;
    width: 100%;
}

.userInfoBox > .name p {
    width: 100%;
    color: #A0A4AE;
    text-align: center;
}

.userInfoBox > .name p.date {
    font-size: 12px;
}

.userInfoBox {
    position: relative;
    width: 100%;
    height: 200px;
    border-radius: 5px;
    overflow: hidden;
    background-image: url('../../assets/img_my_bg.jpg');
    background-repeat: no-repeat;
    background-size: 100% 100%;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
}
</style>



