import Vue from 'vue'
import Router from 'vue-router'
import index from '../pages/index'
import discoverIndex from '../pages/discover/index'
import specialIndex from '../pages/special/index'
import userCenterIndex from '../pages/userCenter/index'
import login from '../pages/login/index'
import specialList from '../pages/special/list'
import specialContent from '../pages/special/content'
import actorList from '../pages/actor/list'
import actorContent from '../pages/actor/content.vue'
import myPaidVideo from '../pages/userCenter/paidVideo/index'
import myCollection from '../pages/userCenter/collection/index'
import searchPage from '../pages/search/index'
import searchResult from '../pages/search/result'
import question from '../pages/userCenter/question/index'
import questionContent from '../pages/userCenter/question/content'
import videoContent from '../pages/videoContent/index_xiguaGetUrlFirst'
import linkPage from '../pages/public/linkPage'
import rechargeIndex from '../pages/userCenter/recharge/index'
import indexInfo from '../pages/indexInfo/index'
import menuPage from '../pages/menuPage/index'
import searchResultForTag from '../pages/search/resultForTag'

Vue.use(Router)

export default new Router({
    routes: [
        {
            path: '/',
            /*name: 'index',*/
            component: index,
            children:[
                {
                    path: '/',
                    name:'indexInfo',
                    component: indexInfo,
                    meta:{
                        //是否缓存
                        keepAlive:true,
                        //是否使用旧数据
                        useOldData:false
                    }
                },
                {
                    path: '/menu/:id',
                    name:'menuPage',
                    component: menuPage,
                    meta:{
                        //是否缓存
                        keepAlive:true,
                        //是否使用旧数据
                        useOldData:false
                    }
                }
            ],
            meta:{
                //是否缓存
                keepAlive:true,
                //是否使用旧数据
                useOldData:false
            }
        },
        {
            path: '/discover',
            name: 'discoverIndex',
            component:discoverIndex,
            meta:{
                //是否缓存
                keepAlive:true,
                //是否使用旧数据
                useOldData:false
            }
        },
        {
            path: '/special',
            name: 'specialIndex',
            component:specialIndex,
            meta:{
                //是否缓存
                keepAlive:true,
                //是否使用旧数据
                useOldData:false
            }
        },
        {
            path: '/specialList',
            name: 'specialList',
            component:specialList,
            meta:{
                //是否缓存
                keepAlive:false,
                //是否使用旧数据
                useOldData:false
            }
        },
        {
            path: '/specialList/:id',
            name: 'specialContent',
            component:specialContent,
            meta:{
                //是否缓存
                keepAlive:true,
                //是否使用旧数据
                useOldData:false
            }
        },
        {
            path: '/actorList',
            name: 'actorList',
            component:actorList,
            meta:{
                //是否缓存
                keepAlive:false,
                //是否使用旧数据
                useOldData:false
            }
        },
        {
            path: '/actorList/:id',
            name: 'actorContent',
            component:actorContent,
            meta:{
                //是否缓存
                keepAlive:true,
                //是否使用旧数据
                useOldData:false
            }
        },
        {
            path: '/userCenter',
            name: 'userCenterIndex',
            component:userCenterIndex,
            meta:{
                //是否缓存
                keepAlive:true,
                //是否使用旧数据
                useOldData:false
            }
        },
        {
            path: '/myPaidVideo',
            name: 'myPaidVideo',
            component:myPaidVideo,
            meta:{
                //是否缓存
                keepAlive:false,
                //是否使用旧数据
                useOldData:false
            }
        },
        {
            path: '/myCollection',
            name: 'myCollection',
            component:myCollection,
            meta:{
                //是否缓存
                keepAlive:false,
                //是否使用旧数据
                useOldData:false
            }
        },
        {
            path: '/login',
            name: 'login',
            component:login,
            meta:{
                //是否缓存
                keepAlive:false,
                //是否使用旧数据
                useOldData:false
            }
        },
        {
            path: '/searchPage',
            name: 'searchPage',
            component:searchPage,
            meta:{
                //是否缓存
                keepAlive:false,
                //是否使用旧数据
                useOldData:false
            }
        },
        {
            path: '/searchResult',
            name: 'searchResult',
            component:searchResult,
            meta:{
                //是否缓存
                keepAlive:false,
                //是否使用旧数据
                useOldData:false
            }
        },
        {
            path: '/searchResultForTag',
            name: 'searchResultForTag',
            component:searchResultForTag,
            meta:{
                //是否缓存
                keepAlive:false,
                //是否使用旧数据
                useOldData:false
            }
        },
        {
            path: '/question',
            name: 'question',
            component:question,
            meta:{
                //是否缓存
                keepAlive:false,
                //是否使用旧数据
                useOldData:false
            }
        },
        {
            path: '/question/:index',
            name: 'questionContent',
            component:questionContent,
            meta:{
                //是否缓存
                keepAlive:false,
                //是否使用旧数据
                useOldData:false
            }
        },
        {
            path: '/videoContent/:id',
            name: 'videoContent',
            component:videoContent,
            meta:{
                //是否缓存
                keepAlive:false,
                //是否使用旧数据
                useOldData:false
            }
        },
        /*视频跳转中转页*/
        {
            path: '/linkPage/:id',
            name: 'linkPage',
            component: linkPage,
            meta:{
                //是否缓存
                keepAlive:false,
                //是否使用旧数据
                useOldData:false
            }
        },
        /*充值*/
        {
            path: '/rechargeIndex',
            name: 'rechargeIndex',
            component: rechargeIndex,
            meta:{
                //是否缓存
                keepAlive:false,
                //是否使用旧数据
                useOldData:false
            }
        }
    ]
})
/*重写replace方法*/
const VueRouterReplace = Router.prototype.replace
Router.prototype.replace = function replace (to) {
    return VueRouterReplace.call(this, to).catch(err => err)
}



