<template>
    <div class="publicAllContentBg" id="linkPage">
        <van-empty image="search" description="跳转中" />
    </div>
</template>

<script>
    export default {
        name: "linkPage",
        methods:{
            getFirstData(){
                console.log('跳转过了');
                /*接收路由参数，跳转*/
                this.$router.replace({name:"videoContent",params:{id:this.$route.params.id}});
            }
        },
        created() {
            this.getFirstData();
        }
    }
</script>

<style scoped>

</style>



