<template>
    <div class="publicAllContentBg fullHeight login" id="login">
        <!--底部登录注册框-->
        <div class="loginButtonBox">
            <!--<div class="bottom" v-if="!showPopup">
                <van-button color="#F54B64" block @click="showPopupFn(1)">登录</van-button>
                <van-button class="regButton" block color="#EAF1FF" @click="showPopupFn(0)">注册</van-button>
            </div>-->
        </div>
        <!--登录弹窗-->
        <van-popup v-model="showPopup"
                   closeable
                   :overlay="false"
                   round
                   @closed="$router.go(-1)"
                   position="bottom">
            <!--注册-->
            <div class="loginFormBg" v-if="formType===0">
                <h3>注册</h3>
                <!--表单-->
                <div class="loginFormBox">
                    <van-field
                        v-model="registerFormConfig.userName"
                        placeholder="请输入您的手机号"
                        autocomplete="off"
                        :border="false"
                    />
                    <van-field
                        v-model="registerFormConfig.password"
                        type="password"
                        autocomplete="off"
                        placeholder="请输入您的密码"
                        :border="false"
                    />
                    <van-field
                        v-model="registerFormConfig.passwordRepeat"
                        type="password"
                        autocomplete="off"
                        placeholder="请重复您的密码"
                        :border="false"
                    />
                    <div class="buttonLine">
                        <van-button
                            @click="onRegister"
                            :loading="registerFormConfig.loading"
                            block
                            type="info"
                            color="linear-gradient(to right, #E6427A, #FC6767)"
                            native-type="submit"
                        >
                            注册账号
                        </van-button>
                    </div>
                </div>
                <!--底部小按钮-->
                <div class="bottomLine">
                    <p class="loginAboutTextLine" @click="formType=1">已有账号，直接登录</p>
                </div>
            </div>
            <!--登录-->
            <div class="loginFormBg" v-if="formType===1">
                <h3>登录</h3>
                <!--表单-->
                <div class="loginFormBox">
                    <van-field
                        v-model="loginFormConfig.userName"
                        placeholder="请输入您的手机号"
                        autocomplete="off"
                        :border="false"
                    />
                    <van-field
                        v-model="loginFormConfig.password"
                        type="password"
                        autocomplete="off"
                        placeholder="请输入您的密码"
                        :border="false"
                    />
                    <div class="buttonLine">
                        <van-button
                            @click="onLogin(1)"
                            :loading="loginFormConfig.loading"
                            block
                            type="info"
                            color="linear-gradient(to right, #E6427A, #FC6767)"
                            native-type="submit"
                        >
                            登录
                        </van-button>
                    </div>
                </div>
                <!--底部小按钮-->
                <div class="bottomLine">
                    <p class="loginAboutTextLine" @click="formType=0">尚未注册？<span>点击注册</span></p>
                    <p class="loginAboutTextLine" @click="formType=3">忘记密码</p>
                </div>
            </div>
            <!--修改密码-->
            <div class="loginFormBg" v-if="formType===2&&$store.state.isLogin">
                <h3>修改密码</h3>
                <!--表单-->
                <div class="loginFormBox">
                    <van-field
                        v-model="changePwdFormConfig.oldPwd"
                        placeholder="请输入您的旧密码"
                        autocomplete="off"
                        :border="false"
                    />
                    <van-field
                        v-model="changePwdFormConfig.password"
                        type="password"
                        autocomplete="off"
                        placeholder="请输入您的新密码"
                        :border="false"
                    />
                    <van-field
                        v-model="changePwdFormConfig.passwordRepeat"
                        type="password"
                        autocomplete="off"
                        placeholder="请重复您的新密码"
                        :border="false"
                    />
                    <div class="buttonLine">
                        <van-button
                            @click="onChangePwd"
                            :loading="changePwdFormConfig.loading"
                            block
                            type="info"
                            color="linear-gradient(to right, #E6427A, #FC6767)"
                            native-type="submit"
                        >
                            修改密码
                        </van-button>
                    </div>
                </div>
                <!--底部小按钮-->
                <div class="bottomLine">
                    <p class="loginAboutTextLine" @click="formType=1" v-if="!$store.state.isLogin">已有账号，直接登录</p>
                </div>
            </div>
            <!--忘记密码-->
            <div class="loginFormBg" v-if="formType===3">
                <h3>忘记密码</h3>
                <!--表单-->
                <div class="loginFormBox" v-if="formType===3">
                    <van-field
                        v-model="forgetPwdFormConfig.userName"
                        placeholder="请输入您的手机号"
                        autocomplete="off"
                        :border="false"
                    />
                    <van-field
                        v-model="forgetPwdFormConfig.userYzm"
                        placeholder="请输入手机号收到的验证码"
                        :border="false"
                    >
                        <template #button>
                            <van-button
                                class="sendCodeButton"
                                v-if="!forgetPwdFormConfig.yzmButtonDisable&&formType===3"
                                size="small"
                                :loading="forgetPwdFormConfig.getYzmButtonLoadingStatus"
                                @click="getYzm"
                                :disabled="forgetPwdFormConfig.yzmButtonDisable"
                            >发送
                            </van-button>
                            <van-button
                                class="doneSendCodeButton"
                                v-if="forgetPwdFormConfig.yzmButtonDisable&&formType===3"
                                size="small"
                                :loading="forgetPwdFormConfig.getYzmButtonLoadingStatus"
                                :disabled="true"
                            >{{ forgetPwdFormConfig.countDownNum }}s
                            </van-button>
                        </template>
                    </van-field>
                    <van-field
                        v-model="forgetPwdFormConfig.password"
                        type="password"
                        autocomplete="off"
                        placeholder="请输入您的密码"
                        :border="false"
                    />
                    <van-field
                        v-model="forgetPwdFormConfig.passwordRepeat"
                        type="password"
                        autocomplete="off"
                        placeholder="请重复您的密码"
                        :border="false"
                    />
                    <div class="buttonLine">
                        <van-button
                            @click="forgetPwdOnSubmit"
                            :loading="forgetPwdFormConfig.loading"
                            block
                            type="info"
                            color="linear-gradient(to right, #E6427A, #FC6767)"
                            native-type="submit"
                        >
                            重置密码
                        </van-button>
                    </div>
                </div>
                <!--底部小按钮-->
                <div class="bottomLine">
                    <p class="loginAboutTextLine" @click="formType=0">尚未注册？<span>点击注册</span></p>
                    <p class="loginAboutTextLine" @click="formType=1">已有账号？立即登录</p>
                </div>
            </div>
        </van-popup>
    </div>
</template>

<script>
export default {
    name: "login",
    computed: {
        /*用户名验证*/
        userNameReg() {
            if (this.$gloabalInfo.regList.phone.test(this.loginFormConfig.userName) === true) {
                return true;
            }
            return false;
        },
        /*密码验证*/
        userPwdReg() {
            return this.loginFormConfig.password !== "";
        }
    },
    data() {
        return {
            /*弹窗是否显示*/
            showPopup: true,
            /*当前显示的表单
            * 0：注册
            * 1：登录
            * 2:修改密码
            * 3:忘记密码(短信验证码修改)
            * */
            formType: 1,
            /*登录表单*/
            loginFormConfig: {
                /*用户账号*/
                userName: "",
                /*用户密码*/
                password: "",
                /*按钮是否在loading*/
                loading: false
            },
            /*注册表单*/
            registerFormConfig: {
                /*用户账号*/
                userName: "",
                /*用户密码*/
                password: "",
                /*重复密码*/
                passwordRepeat: "",
                /*按钮是否在loading*/
                loading: false
            },
            /*修改密码表单*/
            changePwdFormConfig: {
                /*旧密码*/
                oldPwd: "",
                /*用户密码*/
                password: "",
                /*重复密码*/
                passwordRepeat: "",
                /*按钮是否在loading*/
                loading: false
            },
            /*忘记密码表单*/
            forgetPwdFormConfig: {
                /*用户账号*/
                userName: "",
                /*用户密码*/
                password: "",
                /*重复密码*/
                passwordRepeat: "",
                /*验证码*/
                userYzm: "",
                /*按钮是否在loading*/
                loading: false,
                /*发送验证码按钮加载状态*/
                getYzmButtonLoadingStatus: false,
                /*倒计时文字*/
                sendYzmText: "发送",
                /*倒计时数字*/
                countDownNum: 60,
                /*验证码按钮是否可以点击*/
                yzmButtonDisable: false
            }
        }
    },
    methods: {
        /*修改密码提交事件*/
        onChangePwd(){
            /*正则验证*/
            if(this.changePwdFormConfig.oldPwd===""){
                this.$toast.fail("请输入旧密码");
                return;
            }
            if (this.changePwdFormConfig.password === "") {
                this.$toast.fail("请输入新密码");
                return;
            }
            if (this.changePwdFormConfig.password !== this.changePwdFormConfig.passwordRepeat) {
                this.$toast.fail("请正确重复确认密码");
                return;
            }
            /*if(this.loginFormOptions.userYzm===""){
                this.$toast.fail("请输入验证码");
                return;
            }*/
            /*重置密码请求*/
            this.$requestApi.changeUserPwd({
                oldPassword:this.changePwdFormConfig.oldPwd,
                newPassword:this.changePwdFormConfig.password
            })
                .then(res=>{
                    /*删除本地用户信息*/
                    this.$store.commit('changeLoginStatus',false);
                    this.$store.commit('userInfoSave',null);
                    /*清除代理人id*/
                    this.$store.commit('saveAgentID',null);
                    /*清除本地缓存*/
                    localStorage.removeItem('videoMobileDarkStore');
                    /*关闭loading*/
                    this.changePwdFormConfig.loading = false;
                    /*提示成功*/
                    this.$toast.success('修改成功，请重新登录！');
                    this.formType = 1;
                })
                .catch(err=>{
                    /*this.$toast.fail(err.message);*/
                    /*关闭loading*/
                    this.changePwdFormConfig.loading = false;
                })
        },
        /*忘记密码提交事件*/
        forgetPwdOnSubmit() {
            /*正则验证*/
            if (this.$gloabalInfo.regList.phone.test(this.forgetPwdFormConfig.userName) === false) {
                this.$toast.fail("请输入正确的手机号");
                return;
            }
            if (this.forgetPwdFormConfig.password === "") {
                this.$toast.fail("请输入密码");
                return;
            }
            if (this.forgetPwdFormConfig.password !== this.forgetPwdFormConfig.passwordRepeat) {
                this.$toast.fail("请正确重复确认密码");
                return;
            }
            if (this.forgetPwdFormConfig.userYzm === "") {
                this.$toast.fail("请输入验证码");
                return;
            }
            /*重置密码请求*/
            this.$requestApi.resetPassCode({
                userName: this.forgetPwdFormConfig.userName,
                newPassword: this.forgetPwdFormConfig.password,
                code: this.forgetPwdFormConfig.userYzm,
                accountType: 1
            })
                .then(res => {
                    /*删除本地用户信息*/
                    this.$store.commit('changeLoginStatus', false);
                    this.$store.commit('userInfoSave', null);
                    /*清除本地缓存*/
                    localStorage.removeItem('videoMobileDarkStore');
                    /*关闭loading*/
                    this.forgetPwdFormConfig.loading = false;
                    /*提示成功*/
                    this.$toast.success('修改成功，请重新登录！');
                    this.formType = 1;
                })
                .catch(err => {
                    /*关闭loading*/
                    this.forgetPwdFormConfig.loading = false;
                })
        },
        /*发送手机验证码*/
        sendPhoneCode() {
            this.$requestApi.sendPhoneCode({
                userName: this.forgetPwdFormConfig.userName
            })
                .then(res => {
                    this.$toast.success('发送成功！');
                    /*开始倒计时*/
                    this.codeCount();
                })
                .catch(err => {

                })
        },
        /*发送验证码事件*/
        getYzm() {
            if (this.$gloabalInfo.regList.phone.test(this.forgetPwdFormConfig.userName) === false) {
                this.$toast.fail('请输入正确的账号');
                return;
            }
            /*发送手机验证码*/
            this.sendPhoneCode();
        },
        /*倒计时事件*/
        codeCount() {
            if (this.forgetPwdFormConfig.countDownNum === 0) {
                this.forgetPwdFormConfig.yzmButtonDisable = false;
                this.forgetPwdFormConfig.countDownNum = 60;
                window.clearTimeout(this.codeCount);
                return;
            } else {
                this.forgetPwdFormConfig.yzmButtonDisable = true;
                this.forgetPwdFormConfig.countDownNum--;
            }
            setTimeout(() => {
                this.codeCount();
            }, 1000);
        },
        /*注册提交事件*/
        onRegister() {
            /*正则验证*/
            if (this.$gloabalInfo.regList.phone.test(this.registerFormConfig.userName) === false) {
                this.$toast.fail("请输入正确的手机号");
                return;
            }
            if (this.registerFormConfig.password === "") {
                this.$toast.fail("请输入密码");
                return;
            }
            if (this.registerFormConfig.password !== this.registerFormConfig.passwordRepeat) {
                this.$toast.fail("请正确重复确认密码");
                return;
            }
            /*开启loading*/
            this.registerFormConfig.loading = true;
            /*注册*/
            this.$requestApi.register({
                userName: this.registerFormConfig.userName,
                password: this.registerFormConfig.password,
                code: "",
            })
                .then(res => {
                    /*直接登录*/
                    this.onLogin(0);
                })
                .catch(err => {
                    this.$toast.fail(err.message);
                    /*关闭loading*/
                    this.registerFormConfig.loading = false;
                })
        },
        /*显示弹窗*/
        showPopupFn(type) {
            this.formType = type;
            /*显示弹窗*/
            this.showPopup = true;
        },
        /*提交事件*/
        onLogin(type) {
            /*
            * type：0那么就是注册后的自动登录，如果是1就是手动点击登录
            * */
            /*如果是用户手动输入，那么进行正则验证*/
            if (type === 1) {
                if (!this.userNameReg) {
                    this.$toast.fail("请输入正确的账号");
                    return;
                } else if (!this.userPwdReg) {
                    this.$toast.fail("请输入密码");
                    return;
                }
            }

            /*加载动画*/
            this.loginFormConfig.loading = true;
            this.$requestApi.login({
                userName: type === 1 ? this.loginFormConfig.userName : this.registerFormConfig.userName,
                password: type === 1 ? this.loginFormConfig.password : this.registerFormConfig.password,
                // expire:3
            })
                .then((res) => {
                    console.log(res);
                    /*登录成功*/
                    this.$toast.success('登录成功');
                    /*关闭加载动画*/
                    this.loginFormConfig.loading = false;
                    /*关闭注册加载动画*/
                    this.registerFormConfig.loading = false;
                    /*存储用户信息*/
                    this.$store.commit('userInfoSave', res.data);
                    /*改变用户登录状态*/
                    this.$store.commit('changeLoginStatus', true);
                    /*记录用户密码和账号*/
                    this.$store.commit('changeUserAccount', {
                        userName: this.loginFormConfig.userName,
                        password: this.loginFormConfig.password
                    });
                    /*跳转到首页*/
                    this.$router.go(-1);
                })
                .catch(res => {
                    console.log(res);
                    this.$toast.fail(res.message);
                    /*关闭加载动画*/
                    this.loginFormConfig.loading = false;
                    /*关闭注册加载动画*/
                    this.registerFormConfig.loading = false;
                })
        },
    },
    created() {
        /*判断当前路由是否有参数*/
        console.log(this.$route.query.type);
        if(this.$route.query.type!==undefined){
            /*如果有值，那么赋值，然后初始化*/
            this.showPopupFn(2);

        }
    }
}
</script>

<style scoped>
.loginButtonBox {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: flex-end;
    background: linear-gradient(180deg, rgba(36, 42, 55, 0) 0%, rgba(23, 27, 36, 1) 100%);
}

.loginButtonBox > .bottom {
    width: 100%;
    box-sizing: border-box;
    padding: 0 20px 40px 20px;
}

.loginFormBg {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.loginFormBg > h3 {
    color: #C2C8D7;
    font-size: 30px;
    padding-left: 20px;
}

.loginFormBox {
    margin-top: 40px;
    box-sizing: border-box;
    padding: 0 20px;
}

.bottomLine {
    display: flex;
    justify-content: space-between;
    padding: 0 16px;
}

.bottomLine > p {
    color: #F54B64;
    font-size: 14px;
}

.buttonLine {
    margin-top: 20px;
    margin-bottom: 10px;
}
</style>



