<template>
    <div class="publicAllContentBg fullHeight" id="myPaidVideo">
        <van-nav-bar
            :border="false"
            title="收藏视频"
            class="publicAllNavbarClass"
            left-arrow
            @click-left="$router.go(-1)"
        />
        <div class="publicNeedXPaddingBg">
            <!--加载动画-->
            <publicLoadingAnm v-if="!firstLoadingDone"></publicLoadingAnm>
            <!--缺省-->
            <noResult
                v-if="loadingListOptions.list.length===0&&firstLoadingDone"
                desc="暂无视频" />
            <!--视频列表-->
            <van-list
                v-if="loadingListOptions.list.length>0&&firstLoadingDone"
                v-model="loadingListOptions.loading"
                :finished="loadingListOptions.finished"
                @load="onLoad"
                :immediate-check="false"
            >
                <publicVideoListModule :video-list="loadingListOptions.list" :need-clear="false"></publicVideoListModule>
            </van-list>
        </div>

    </div>
</template>

<script>
import aesImage from "../../../components/aesImage";
import publicVideoListModule from "../../../components/publicVideoListModule";
import publicLoadingAnm from "../../../components/publicLoadingAnm";
import noResult from "../../../components/noResult";
export default {
    name: "myCollection",
    components:{
        aesImage,
        publicLoadingAnm,
        publicVideoListModule,
        noResult
    },
    computed:{
    },
    data(){
        return {
            /*当前是否是编辑模式*/
            isEditMode:false,
            /*第一次进入加载loading*/
            firstLoadingDone:false,
            /*加载更多配置*/
            loadingListOptions:{
                /*是否在加载*/
                loading:false,
                /*是否已经结束*/
                finished:false,
                /*列表*/
                list:[
                    /*{
                        id: 1,
                        name: "第六届科幻联想儿童创意大赛官方规则说明",
                        img: require('../../../assets/indexBanner.jpg'),
                        type: 1,
                        hot: 1000,
                        time: "10:56",
                        percentage:50,
                        createTime:"04-26 17:45",
                        needVip:true,
                        active:false
                    }*/
                ],
                /*当前页数*/
                current:1,
                /*总页数*/
                totalPage:0,
                /*总条数*/
                totalCount:0,
                /*每页条数*/
                pageSize:20
            }
        }
    },
    methods:{
        /*获取列表*/
        getList(type){
            /*请求*/
            this.$requestApi.getUserVideoCollection({
                length:this.loadingListOptions.pageSize,
                page:type===1?this.loadingListOptions.current+1:1
            })
                .then(res=>{
                    if(res.data.list.length===0){
                        /*关闭加载动画*/
                        this.firstLoadingDone=true;
                        return;
                    }
                    if(type===0){
                        this.loadingListOptions.list=res.data.list;
                    }
                    else{
                        this.loadingListOptions.list=this.loadingListOptions.list.concat(res.data.list);
                    }
                    /*放置总页数*/
                    this.loadingListOptions.totalPage=this.$globalFn.totalPageCountFn(this.loadingListOptions.pageSize,res.data.recordsTotal);
                    /*放置当前页数*/
                    this.loadingListOptions.current=res.data.page;
                    /*记录总条数*/
                    this.loadingListOptions.totalCount=res.data.recordsTotal;
                    console.log("打印当前页数");
                    console.log(this.loadingListOptions.current);
                    console.log("打印当前总页数");
                    console.log(this.loadingListOptions.totalPage);
                    /*判断是否已经到最后一页，如果是，那么就停止下拉加载*/
                    if(this.loadingListOptions.totalPage===this.loadingListOptions.current){
                        this.loadingListOptions.finished=true;
                    }
                    else if(this.loadingListOptions.totalPage===1){
                        this.loadingListOptions.finished=true;
                    }
                    /*关闭加载动画*/
                    this.loadingListOptions.loading=false;
                    /*关闭第一次加载*/
                    this.firstLoadingDone=true;
                })
                .catch(err=>{
                    /*关闭第一次加载*/
                    this.firstLoadingDone=true;
                })
        },
        /*去视频详情*/
        goToVideoContent(item){
            this.$router.push({name:"videoContent",params:{id:item.videoId}});
        },
        onLoad(){
            this.getList(1);
        },
        /*点击右侧按钮*/
        clickRightFn(){
            if(this.loadingListOptions.list.length===0){
                return;
            }
            if(this.isEditMode===true){
                this.isEditMode=false;
            }
            else if(this.isEditMode===false){
                this.isEditMode=true;
            }
        },
        /*全选方法*/
        selectAll(){

        },
        /*确认删除按钮*/
        delConfirm(){
            this.$dialog.confirm({
                title: '操作确认',
                message: '是否确认删除',
                overlayClass:"logoutPopup"
            })
                .then(() => {

                })
                .catch(() => {
                    // on cancel
                });
        }
    },
    created() {
        this.getList(0);
    }
}
</script>

<style scoped>
.bottomFixedBar>p.del{
    color: #CB266D;
}
.bottomFixedBar>p{
    height: 100%;
    line-height:50px;
    font-size: 14px;
    padding: 0 10px;
    color: #646464;
}
.bottomFixedBar{
    width: 100%;
    height:50px;
    box-sizing: border-box;
    border-top: 1px solid #f3f3f3;
    display: flex;
    justify-content: space-between;
    position: fixed;
    bottom: 0;
    left: 0;
    background-color: #FFFFFF;
}
.historyListStyle>div.del{
    display: flex;
    align-items: center;
    margin-right: 10px;
}
.historyListStyle>div.content>div.right>.bottom>p:first-child{
    margin-left: 0;
}
.historyListStyle>div.content>div.right>.bottom>p{
    margin-left: 4px;
}
.historyListStyle>div.content>div.right>.bottom{
    display: flex;
    align-items: center;
    font-size: 11px;
    color: #979797;
}
.historyListStyle>div.content>div.right>.top>span{
    padding: 4px 6px;
    box-sizing: border-box;
    border-radius: 5px;
    background:linear-gradient(134deg,rgba(230,66,122,1) 0%,rgba(252,103,103,1) 100%);
    color: #FFFFFF;
    font-size: 8px;
    display: inline-block;
    margin-left: 4px;
}
.historyListStyle>div.content>div.right>.top{
    color: #403F3F;
    font-size: 12px;
}
.historyListStyle>div.content>div.right{
    padding-left: 6px;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.historyListStyle>div.content>div.left img{
    width: 100%;
}
.historyListStyle>div.content>div.left{
    width: 120px;
    position: relative;
    flex-shrink: 0;
    height: 66px;
}
.historyListStyle>div.content{
    width: 100%;
    height: auto;
    box-sizing: border-box;
    display: flex;
}
.historyListStyle{
    width: 100%;
    height: auto;
    box-sizing: border-box;
    display: flex;
}
.historyList>li:first-child{
    margin-top: 0;
}
.historyList>li{
    margin-top:10px;
}
.historyList{
    padding:10px 10px;
    box-sizing: border-box;
}
</style>



