<template>
    <div class="publicAllContentBg specialArtistModule" id="publicSpecialArtistModule">
        <!--艺人信息-->
        <div class="topLine">
            <div class="left">
                <div class="icon" @click="goArtistContent(moduleInfo.id)">
                    <aesImage :primordial-img="true" :file-url="moduleInfo.icon"></aesImage>
                </div>
                <div class="info" @click="goArtistContent(moduleInfo.id)">
                    <p class="name">{{ moduleInfo.name }}</p>
                    <p class="length">收录视频<span>{{ moduleInfo.videoCount }}</span>部</p>
                </div>

            </div>
            <!--更多按钮-->
            <div class="right" @click="goArtistContent(moduleInfo.id)">
                <van-icon name="ellipsis" size="20px" color="#A0A4AE"/>
            </div>
        </div>
        <!--暂无视频-->
        <noResult desc="暂无结果" v-if="videoList.length===0"></noResult>
        <!--视频列表-->
        <publicVideoListModule v-if="videoList.length>0" :video-list="videoList"></publicVideoListModule>
    </div>
</template>

<script>
import noResult from "./noResult";
import aesImage from "./aesImage";
import publicVideoListModule from "./publicVideoListModule";

export default {
    name: "publicSpecialArtistModule",
    props: {
        /*
        *   {
        *       name:""  //艺人名称
        *       id:""   //艺人id
        *       intro:""  //艺人介绍
        *       icon:""  //艺人头像
        *   }
        * */
        moduleInfo: Object,
        videoList: Array
    },
    components: {
        publicVideoListModule,
        aesImage,
        noResult
    },
    data() {
        return {}
    },
    methods: {
        /*艺人首页*/
        goArtistContent(id) {
            this.$router.push({name: "actorContent", params: {id:id}});
        },
    },
    created() {
    }
}
</script>

<style scoped>
.specialArtistModule{
    background-color: #2B3243;
    box-sizing: border-box;
    padding: 10px;
}
.specialArtistModule > .topLine {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 10px;
}

.specialArtistModule > .topLine .icon {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    overflow: hidden;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    position: relative;
    margin-right: 10px;
}
.specialArtistModule > .topLine .name{
    color: #EAF1FF;
    font-size: 16px;
}
.specialArtistModule > .topLine  .length{
    color: #A0A4AE;
    font-size: 12px;
}
.specialArtistModule > .topLine  .length span{
    margin: 0 2px;
}
.specialArtistModule > .topLine >.left{
    width: 95%;
    display: flex;
    align-items: center;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
}
.specialArtistModule > .topLine >.right{
    width: 5%;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    height: 100%;
}
</style>



