<template>
    <fragment>
        <van-image
            @error="error"
            :fit="imgFitType===undefined?'cover':imgFitType" :src="base64" v-if="primordialImg===false" class="aseImgVanImage">
            <template v-slot:loading>
                <van-loading type="spinner" size="20" />
            </template>
            <template v-slot:error>
                <van-loading type="spinner" size="20" />
            </template>
        </van-image>
        <img :src="base64" alt="" v-if="primordialImg===true" class="aesImgTag">
    </fragment>
</template>

<script>
    import { Fragment } from 'vue-fragment'
    import CryptoJS from '../../static/lib/crypto-js.min'

    export default {
        name: "aesImage",
        components:{
            Fragment
        },
        props: {
            /*aes加密文件地址*/
            fileUrl: String,
            /*是否需要原生img标签*/
            primordialImg:Boolean,
            /*上级元素名称*/
            topEl:String,
            /*图片填充方法*/
            imgFitType:String
        },
        data() {
            return {
                /*公共秘钥*/
                aesKey: "46cc793c53dc451b",
                /*最终显示的base64字符串*/
                base64: null
            }
        },
        methods: {
            /*图片加载错误*/
            error(){
                this.getCode();
            },
            /*解密方法*/
            decryptFn(word) {
                /*公共秘钥，这边先写死*/
                let key = CryptoJS.enc.Utf8.parse(this.aesKey);
                let decrypt = CryptoJS.AES.decrypt(word, key, {
                    /*以下两个属性需要和加密对应*/
                    mode: CryptoJS.mode.ECB,
                    padding: CryptoJS.pad.Pkcs7
                });
                return CryptoJS.enc.Utf8.stringify(decrypt).toString();
            },
            /*请求文件*/
            getCode(){
                this.$axios({
                    url:this.fileUrl,
                    method:"GET"
                })
                .then(res=>{
                    this.base64=this.decryptFn(res.data);
                })
                .catch(err=>{

                })
            },
            /*初始化图片*/
            getFirstData(){
                /*先置空*/
                this.base64="";
                // console.log(this.fileUrl);
                /*判断当前传递的url的后缀名*/
                /*console.log(this.fileUrl.split('.')[this.fileUrl.split('.').length-1]);*/
                /*如果文件后缀名等于aes，那么启用解密程序，否则直接赋值地址*/
                if(this.fileUrl.split('.')[this.fileUrl.split('.').length-1]==="aes"){
                    this.getCode();
                    return;
                }
                // console.log('原生图片');
                this.base64=this.fileUrl;
            }
        },
        created() {
            /*console.log('创建1次');
            console.log(this.fileUrl);*/
            this.getFirstData();
        },
        watch:{
            fileUrl(val){
                /*重新触发图片方法*/
                this.getFirstData();
            }
        }
    }
</script>

<style scoped>
</style>



