<template>
    <div class="publicAllContentBg fullHeight">
        <!--顶部tabbar-->
        <van-nav-bar
            :title="topTitle"
            class="publicAllNavbarClass"
            :border="false"
            left-arrow
            @click-left="$router.go(-1)"
        />
        <!--底部内容-->
        <div class="publicNeedXPaddingBg">
            <ul class="bottomContentList">
                <li v-for="(item,index) in questionList">
                    <div class="bottomContentListStyle">
                        <p class="top">{{ item.title }}</p>
                        <p class="bottom">{{ item.content }}</p>
                    </div>
                </li>
            </ul>
        </div>

    </div>
</template>

<script>
import questionList from '../../../../static/lib/questions.json'
export default {
    name: "questionContent",
    data(){
        return {
            /*顶部标题*/
            topTitle:"读取中",
            /*底部内容*/
            questionList:[]
        }
    },
    created() {
        /*赋值标题*/
        this.topTitle=questionList[this.$route.params.index].name;
        /*赋值底部内容*/
        this.questionList=questionList[this.$route.params.index].list;
    }
}
</script>

<style scoped>
.bottomContentListStyle>.bottom{
    color: #797D88;
    font-size: 14px;
    font-weight:400;
    margin-top: 10px;
    white-space: pre-wrap;
}
.bottomContentListStyle>.top{
    color: #A0A4AE;
    font-size: 16px;
    font-weight:500;
}
.bottomContentList>li{
    margin-top: 15px;
    border-top: 1px solid #303542;
    padding-top: 15px;
    box-sizing: border-box;
}
.bottomContentList>li:first-child{
    margin-top: 0;
    border-top: none;
}
</style>



