<template>
    <div class="publicAllContentBg fullHeight" id="artistIndex" ref="artistIndex">
        <!--固定顶部栏-->
        <van-nav-bar
            class="publicAllNavbarClass"
            left-arrow
            @click-left="onClickLeft"
            :border="false"
            :title="artistInfo.name"
        >
        </van-nav-bar>
        <div class="publicNeedXPaddingBg">
            <!--loading-->
            <publicLoadingAnm v-if="!firstLoadingDone"></publicLoadingAnm>
            <!--视频列表-->
            <van-list
                v-if="loadingListOptions.list.length>0&&firstLoadingDone"
                v-model="loadingListOptions.loading"
                :finished="loadingListOptions.finished"
                @load="onLoad"
                :immediate-check="false"
            >
                <publicVideoList :videoList="loadingListOptions.list"></publicVideoList>
            </van-list>
            <noResult desc="暂无视频" v-if="loadingListOptions.list.length===0&&firstLoadingDone"></noResult>
        </div>

    </div>
</template>

<script>
import noResult from "../../components/noResult";
import publicLoadingAnm from "../../components/publicLoadingAnm";
import aesImage from "../../components/aesImage";
import publicVideoList from "../../components/publicVideoListModule";

export default {
    name: 'artistIndex',
    components: {
        publicVideoList,
        aesImage,
        publicLoadingAnm,
        noResult
    },
    methods: {
        /*获取视频*/
        getList(type) {
            this.$requestApi.getVideoFromArtist({
                actorId: this.$route.params.id,
                length: this.loadingListOptions.pageSize,
                page: type === 1 ? this.loadingListOptions.current + 1 : 1,
            })
                .then(res => {
                    /*放置视频数据*/
                    type === 0 ? this.loadingListOptions.list = res.data.list : this.loadingListOptions.list = this.loadingListOptions.list.concat(res.data.list);
                    /*放置总页数*/
                    this.loadingListOptions.totalPage = this.$globalFn.totalPageCountFn(this.loadingListOptions.pageSize, res.data.recordsTotal);
                    /*放置当前页数*/
                    this.loadingListOptions.current = res.data.page;
                    /*记录总条数*/
                    this.loadingListOptions.totalCount = res.data.recordsTotal;
                    /*console.log("打印当前页数");
                    console.log(this.loadingListOptions.current);
                    console.log("打印当前总页数");
                    console.log(this.loadingListOptions.totalPage);*/
                    /*判断是否已经到最后一页，如果是，那么就停止下拉加载*/
                    if (this.loadingListOptions.totalPage === this.loadingListOptions.current) {
                        this.loadingListOptions.finished = true;
                    } else if (this.loadingListOptions.totalPage === 1) {
                        this.loadingListOptions.finished = true;
                    }
                    /*关闭加载动画*/
                    this.loadingListOptions.loading = false;
                    this.firstLoadingDone = true;
                })
                .catch(err => {

                })
        },
        /*请求艺人详情*/
        getArtistContent() {
            this.$requestApi.getArtistContent({
                actorId: this.$route.params.id
            })
                .then(res => {
                    /*放置演员名称*/
                    this.artistInfo.name = res.data.info.name;
                    /*放置演员id*/
                    this.artistInfo.id = res.data.info.id;
                    /*放置图片*/
                    this.artistInfo.photo = res.data.info.headIco;
                    /*放置身高*/
                    this.artistInfo.height = res.data.info.height;
                    /*放置简介*/
                    this.artistInfo.intro = res.data.info.description;
                    /*放置体重*/
                    this.artistInfo.age = res.data.info.waist;
                    /*放置罩杯*/
                    this.artistInfo.cup = res.data.info.cup;
                })
                .catch(err => {
                    console.log(err);
                })
        },
        /*加载更多视频*/
        onLoad() {
            this.getList(1);
        },
        /*返回上一页*/
        onClickLeft() {
            this.$router.go(-1);
        },
        /*获取位置*/
        publicReturnScrollPos() {
            console.log('打印位置');
            console.log(this.$refs.artistIndex.scrollTop);
            return this.$refs.artistIndex.scrollTop;
        },
        /*更多*/
        onClickRight() {
        },
        /*重置页面的方法*/
        resetData() {
            this.loadingListOptions.finished = false;
            this.loadingListOptions.loading = true;
            this.loadingListOptions.totalCount = 0;
            this.loadingListOptions.current = 1;
            this.firstLoadingDone = false;
            this.loadingListOptions.list = [];
        },
    },
    created() {
        this.getArtistContent();
        this.getList(0);
    },
    beforeRouteEnter(to, from, next) {
        /*如果不是来自视频详情，那么就需要请求信数据，反之就不需要*/
        if (from.name !== "videoContent") {
            to.meta.useOldData = false;
        } else {
            to.meta.useOldData = true;
        }
        next();
    },
    beforeRouteLeave(to, from, next) {
        console.log('打印取的位置');
        console.log(to);
        /*如果是去详情，那么记住滚动位置*/
        if (to.name === "linkPage") {
            this.$store.commit('changeScrollPos', this.publicReturnScrollPos());
            /*console.log('打印离开钱位置');
            console.log(this.$globalFn.publicReturnScrollPos())*/
            next();
        }
        next();
    },
    activated() {
        if (this.$route.meta.useOldData === false) {
            this.resetData();
            this.getArtistContent();
            this.getList(0);
        }
        //重置防止出错
        this.$route.meta.useOldData = false;
        /*激活(页面被缓存的情况下)*/
        this.$refs.artistIndex.scrollTop = this.$store.state.scrollPosition;
    },
    data() {
        return {
            /*第一次加载动画*/
            firstLoadingDone: false,
            /*加载更多配置*/
            loadingListOptions: {
                /*是否在加载*/
                loading: false,
                /*是否已经结束*/
                finished: false,
                /*列表*/
                list: [],
                /*当前页数*/
                current: 1,
                /*总页数*/
                totalPage: 0,
                /*总条数*/
                totalCount: 0,
                /*每页条数*/
                pageSize: 20
            },
            /*艺人信息*/
            artistInfo: {
                /*艺人id*/
                id: 1,
                /*艺人名称*/
                name: "读取中",
                /*艺人照片*/
                photo: "",
                /*身高*/
                height: "",
                /*年龄*/
                age: "",
                /*是否收藏*/
                isCollected: false,
                /*获得奖项*/
                price: [],
                /*艺人简介*/
                intro: "读取中",
                /*罩杯*/
                cup: ""
            }
        }
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.artistInfoLine {
    width: 100%;
    height: auto;
    overflow: hidden;
    box-sizing: border-box;
    padding: 0 10px 10px 10px;
    color: #646464;
    font-size: 14px;
    margin-top: 10px;
}

.artistBox > .bottomRectangle {
    position: absolute;
    width: 100%;
    height: 10px;
    background-color: #ffffff;
    bottom: 0;
    left: 0;
    border-radius: 20px 20px 0 0;
}

.artistBox div.two > div.right {
    display: flex;
    align-items: center;
}

.artistBox div.two > div.right > p:first-child {
    margin-left: 0;
}

.artistBox div.two > div.right > p {
    padding: 4px 8px;
    color: #ffffff;
    height: 20px;
    border-radius: 50px;
    font-size: 14px;
    background-color: rgba(203, 38, 109, 0.65);
    margin-left: 10px;
}

.artistBox div.two > div.left {
    align-items: center;
}

.artistBox div.two > div.left p {
    color: #ffffff;
    font-size: 14px;
}

.artistBox div.two {
    width: 100%;
    position: absolute;
    bottom: 0;
    left: 0;
    padding: 0 10px 30px 10px;
    box-sizing: border-box;
    display: flex;
    justify-content: space-between;
}

.artistBox div.one {
    align-self: center;
    box-sizing: border-box;
    padding: 0 10px;
}

.artistBox div.one p {
    color: #FFFFFF;
    font-size: 34px;
    margin-bottom: 4px;
}

.artistBox div.one .button {
    display: inline-block;
    padding: 8px 12px;
    color: #CB266D;
    border-radius: 50px;
    background-color: #fff;
    font-size: 14px;
    background-color: rgba(255, 255, 255, 0.65);
}

.artistBox {
    width: 100%;
    height: auto;
    overflow: hidden;
    box-sizing: border-box;
    position: relative;
    display: flex;

}

.artistBox > div.img {
    width: 100%;
    height: 100%;
}

.artistBox > div.popup {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(230, 66, 122, 0.4);
    display: flex;
    /*flex-direction: column;*/
}

</style>



