<template>
    <div id="app">
        <keep-alive>
            <router-view v-if="$route.meta.keepAlive"/>
        </keep-alive>
        <router-view v-if="!$route.meta.keepAlive"/>
        <!--底部tabbar-->
        <van-tabbar v-model="bottomTabBarOptions.active"
                    active-color="#F54B64"
                    class="publicBottomTabbar"
                    inactive-color="#A0A4AE"
                    v-show="tabBarShowStatus">
            <van-tabbar-item
                v-for="(item,index) in bottomTabBarOptions.list"
                :key="index"
                replace
                :to="item.to"
            >
                <span>{{ item.text }}</span>
                <template #icon="props">
                    <img :src="props.active ? item.activeIcon : item.icon"/>
                </template>
            </van-tabbar-item>
        </van-tabbar>
    </div>
</template>

<script>
export default {
    name: 'App',
    computed:{
        tabBarShowStatus() {
            /*首先判断最大条件*/
            if (!this.$store.state.showTabbar) {
                return false;
            }
            switch (this.$route.name) {
                case 'indexInfo':
                    return true;
                case 'menuPage':
                    return true;
                case 'discoverIndex':
                    return true;
                case 'userCenterIndex':
                    return true;
                case 'specialIndex':
                    return true;
                default:
                    return false;
            }
        }
    },
    data(){
        return {
            /*底部栏配置*/
            bottomTabBarOptions: {
                /*当前选中*/
                active: 0,
                /*导航list*/
                list: [
                    {
                        name: "home",
                        text: "首页",
                        icon: require("../src/assets/indexBottomIcon.png"),
                        activeIcon: require("../src/assets/indexBottomIconActive.png"),
                        to: '/'
                    },
                    {
                        name: "search",
                        text: "发现",
                        icon: require("../src/assets/discoverBottomIcon.png"),
                        activeIcon: require("../src/assets/discoverBottomIconActive.png"),
                        to: '/discover'
                    },
                    {
                        name: "friends",
                        text: "专题",
                        icon: require("../src/assets/specialBottomIcon.png"),
                        activeIcon: require("../src/assets/specialBottomIconActive.png"),
                        to: "/special"
                    },
                    {
                        name: "setting",
                        text: "我的",
                        icon: require("../src/assets/userBottomIcon.png"),
                        activeIcon: require("../src/assets/userBottomIconActive.png"),
                        to: "/userCenter"
                    }
                ]
            }
        }
    },
    methods:{
        /*底部路由初始化的方法*/
        bottomUrlConfirm() {
            console.log('获取当前路由');
            console.log(this.$route.name);
            switch (this.$route.name) {
                case 'indexInfo':
                    this.bottomTabBarOptions.active = 0;
                    break;
                case 'menuPage':
                    this.bottomTabBarOptions.active = 0;
                    break;
                case 'discoverIndex':
                    this.bottomTabBarOptions.active = 1;
                    break;
                case 'specialIndex':
                    this.bottomTabBarOptions.active = 2;
                    break;
                case 'userCenterIndex':
                    this.bottomTabBarOptions.active = 3;
                    break;
            }
        },
        /*获取配置信息*/
        getConfigInfo() {
            this.$requestApi.getConfig({})
                .then(res => {
                    console.log('打印配置信息');
                    console.log(res);
                    /*放置到store*/
                    this.$store.commit('configInfoSave', res.data);
                    /*执行判断设备方法*/
                    /*this.$globalFn.returnDeviceShouldVisit();*/
                })
                .catch(err => {

                })
        },
        /*重新获取用户信息的方法*/
        getUserInfo() {
            this.$requestApi.getUserInfo({})
                .then(res => {
                    /*存储用户信息*/
                    this.$store.commit('userInfoSave', res.data);
                })
                .catch(err => {

                })
        },
    },
    created() {
        this.getConfigInfo();
        this.bottomUrlConfirm();
    },
    mounted() {
        /*如果已经登录，重新获取用户信息*/
        if (this.$store.state.isLogin === true) {
            this.getUserInfo();
        }
    },
    watch:{
        '$route'(val) {
            this.bottomUrlConfirm();
        }
    }
}
</script>

<style>
</style>



