<template>
    <div class="publicAllContentBg fullHeight" id="rechargeIndex">
        <van-nav-bar
            :border="false"
            title="充值"
            class="publicAllNavbarClass"
            left-arrow
            @click-left="$router.go(-1)"
        />
        <div class="publicNeedXPaddingBg">
            <!--充值列表-->
            <van-tabs v-model="nowSelectGoodsType" title-active-color="#F54B64" title-inactive-color="#EAF1FF" color="#F54B64" background="transparent">
                <van-tab title="VIP卡充值" :name="1">
                    <ul class="rechargeList">
                        <li v-for="(item,index) in rechargeList.vip" :key="index" @click="goRechargeChannel(item)">
                            <div :class="item.active===true?'rechargeStyle active':'rechargeStyle'">
                                <p class="top"><span>￥</span>{{item.price}}</p>
                                <p class="bottom">{{item.name}}</p>
                            </div>
                        </li>
                    </ul>
                </van-tab>
                <van-tab title="钻石充值" :name="2">
                    <ul class="rechargeList">
                        <li v-for="(item,index) in rechargeList.point" :key="index" @click="goRechargeChannel(item)">
                            <div :class="item.active===true?'rechargeStyle active':'rechargeStyle'">
                                <p class="top"><span>￥</span>{{item.price}}</p>
                                <p class="bottom">{{item.name}}</p>
                            </div>
                        </li>
                    </ul>
                </van-tab>
            </van-tabs>


            <!--充值方式-->
            <van-tabs v-model="nowSelectRechargeType" title-active-color="#F54B64" title-inactive-color="#EAF1FF" color="#F54B64" background="transparent">
                <van-tab title="微信支付" :name="1">
                    <!--微信支付选项-->
                    <ul class="rechargeTypeList">
                        <li v-for="(item,index) in weChatRechargeTypeList" :key="index">
                          <p :class="{active:item.paymentId==selectPaymentId}" @click="goRecharge(item)">{{item.name}}</p>
                        </li>
                    </ul>
                </van-tab>
                <van-tab title="支付宝支付" :name="2">
                    <!--微信支付选项-->
                    <ul class="rechargeTypeList">
                        <li v-for="(item,index) in aliPayRechargeTypeList" :key="index">
                          <p :class="{active:item.paymentId==selectPaymentId}" @click="goRecharge(item)">{{item.name}}</p>
                        </li>
                    </ul>
                </van-tab>
            </van-tabs>
            <!--确认充值按钮-->
            <van-button type="primary" @click="getPrepareRecharge(selectItem)" color="#F54B64" block style="margin-top: 10px">立即支付</van-button>
            <!--tip-->
            <ul class="tipList">
                <li v-for="(item,index) in tipList" :key="index">
                    <p>{{index+1}}.{{item}}</p>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
export default {
    name: "rechargeIndex",
    data(){
        return {
            /*目前选中充值方式(1微信 2支付宝)*/
            nowSelectRechargeType:1,
            nowSelectGoodsType:1,
            /*目前选中的微信或支付宝的选项id*/
            selectPaymentId:0,
            /*充值tip*/
            tipList:[
                "建议您在充值之前先注册登录，以便在更换设备时可同步帐号资料，请在栏目进行注册登录；",
                "钻石充值成功后，不能退款、提现或转赠他人；",
                "购买后若长时间无变化，请点击左侧栏目-在线客服进行处理；"
            ],
            /*支付宝充值套餐*/
            aliPayRechargeTypeList:[],
            /*微信充值套餐*/
            weChatRechargeTypeList:[],
            /*目前选中的套餐id*/
            selectedRechargeId:1,
            /*充值列表*/
            rechargeList:{}
        }
    },
    methods:{
        getRechargeList(){
          this.$requestApi.getRechargeList({
              type:2,
              clientType:1,
          })
              .then(res=>{
                  console.log('打印');
                  console.log(res)
                  /*放置列表*/
                  this.rechargeList=res.data;

              })
              .catch(err=>{
              })
        },
        goRechargeChannel(item){
          for(let obj of this.rechargeList.point){
              if(obj.id===item.id){
                  continue;
              }
              obj.active=false;
          }
          for(let obj of this.rechargeList.vip){
              if(obj.id===item.id){
                  continue;
              }
              obj.active=false;
          }
          item.active=true;
          this.selectedRechargeId=item.id;
          this.aliPayRechargeTypeList=item.payments.alipay
          this.weChatRechargeTypeList=item.payments.wc
        },

        goRecharge(item){
          console.log('.item',item)
          console.log(this.selectPaymentId)
          this.selectPaymentId=item.paymentId;
          this.selectItem=item;
          this.$forceUpdate();
        },
        getPrepareRecharge(item){
          this.$requestApi.getPrepareRecharge({
              goodsId:item.goodsId,
              paymentId:item.paymentId,
              clientType:1,
          })
              .then(res=>{
                  console.log('打印');
                  console.log(res)
                  window.open(res.data.url, '_blank');
                  /*放置列表*/
              })
              .catch(err=>{
              })
        },
    },
    created() {
        this.getRechargeList();
    }
}
</script>

<style scoped>
.rechargeTypeList{
    display: flex;
    flex-wrap: wrap;
    margin-top: 10px;
}
.rechargeTypeList>li{
    width: 32%;
    box-sizing: border-box;
    /*justify-content: space-between;*/
}
.rechargeTypeList>li:nth-child(2){
    margin: 0 5px;
}
.rechargeTypeList>li p{
    border: 1px solid #2B303B;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    background-color: #2B303B;
    font-size: 13px;
    /*padding: 20px 40px;*/
    width: 100%;
    height: 74px;
    line-height: 74px;
    text-align: center;
    cursor: pointer;
    border-radius: 4px;
    color: #fafafa;
}
.rechargeTypeList>li p.active{
    border-color: #F54B64;
}
.tipList p{
    color: #5A5F6A;
    font-size: 14px;
}
.tipList li:first-child{
    margin-top: 0;
}
.tipList li{
    margin-top: 10px;
}
.tipList{
    margin: 10px 0;
}
.rechargeStyle p.top span{
    font-size: 16px;
}
.rechargeStyle p.bottom{
    color: #FFFFFF;
    font-size: 14px;
    font-weight: 400;
    text-align: center;

}
.rechargeStyle p.top{
    color: #F54B64;
    font-size: 36px;
    font-weight: 400;
    text-align: center;
}
.rechargeList{
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 20px;
}
.rechargeStyle.active{
    border-color: #F54B64;
}
.rechargeStyle:hover{
    border-color: #F54B64;
}
.rechargeStyle{
    border: 1px solid #2B303B;
    box-sizing: border-box;
    background-color: #2B303B;
    padding: 20px 40px;
    cursor: pointer;
    border-radius: 4px;
}
.rechargeList>li{
    width: 50%;
    box-sizing: border-box;
    padding:5px 5px;
}
.rechargeList>li:nth-child(1){
    padding-left: 0;
    padding-top: 0;
}
.rechargeList>li:nth-child(2){
    padding-right: 0;
    padding-top: 0;
}
.rechargeList>li:nth-child(3){
    padding-left: 0;
    padding-bottom: 0;
}
.rechargeList>li:nth-child(4){
    padding-right: 0;
    padding-bottom: 0;
}
</style>



