<template>
    <!--首页的视频模块-->
    <div class="publicIndexVideoMoudleBox">
        <!--顶部标题-->
        <!--标题-->
        <div class="publicContentHeaderBox">
            <div class="left">
                <van-icon :name="moduleInfo.icon"></van-icon>
                {{moduleInfo.name}}
            </div>
            <!--<div class="right">
                查看更多
            </div>-->
        </div>
        <!--视频列表-->
        <publicVideoListModule :video-list="videoList" v-if="videoList.length>0"></publicVideoListModule>
        <!--缺省-->
        <noResult v-if="videoList.length===0"></noResult>
        <!--分割线-->
        <!--<div class="videoMoudleLine" v-if="videoList.length>0"></div>-->
        <!--更多和换一批-->
        <div class="bottom" v-if="videoList.length>0">
            <div class="more" @click="goForMoreFn">
                <van-icon name="add-o" color="#A0A4AE" size="16px" style="margin-right: 4px"/>
                更多
            </div>
            <div class="change" @click="changeFn">
                <van-icon name="replay" color="#A0A4AE" size="16px" style="margin-right: 4px"/>
                换一批
            </div>
        </div>
    </div>
</template>

<script>
import publicVideoListModule from "./publicVideoListModule";
import aesImage from "./aesImage";
import noResult from "./noResult";

export default {
    name: "indexVideoModule",
    props: {
        moduleInfo: Object,
        videoList: Array
    },
    components: {
        aesImage,
        publicVideoListModule,
        noResult,
    },
    data() {
        return {}
    },
    methods: {
        /*换一批*/
        changeFn() {
            this.$emit('changeAnother');
        },
        /*去往更多*/
        goForMoreFn() {
            this.$emit('lookForMore');
        },
        /*去视频详情*/
        goToVideoContent(item) {
            this.$router.push({name: 'videoContent', params: {id: item.id}});
        }
    },
    created() {

    }
}
</script>

<style scoped>
.publicContentHeaderBox {
    width: 100%;
    height: auto;
    overflow: hidden;
    box-sizing: border-box;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 0;
}

.publicContentHeaderBox > div.left {
    color: #EAF1FF;
    font-size: 18px;
    display: flex;
    align-items: center;
}

.publicContentHeaderBox > div.left i {
    margin-right: 10px;
}

.publicContentHeaderBox > div.right {
    color: #A0A4AE;
    display: flex;
    align-items: center;
    font-size: 14px;
}

/*具体video样式*/
.videoMoudleLine {
    width: 100%;
    height: 2px;
    background-color: #F7F7F7;
}

/*.publicIndexVideoMoudleBox > .bottom > div.more {
    border-right: 2px solid #F7F7F7;
}*/

.publicIndexVideoMoudleBox > .bottom > div {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 49%;
    padding: 10px 0;
    background-color: #2C3343;
}

.publicIndexVideoMoudleBox > .bottom {
    width: 100%;
    margin-top: 2px;
    font-size: 14px;
    color: #A0A4AE;
    display: flex;
    justify-content: space-between;
}

.videoListStyle > .title {
    color: #403f3f;
    font-size: 14px;
    height: 40px;
    line-height: 20px;
    overflow: hidden;
}

.videoListStyle > .imgArea > .bottomInfo {
    width: 100%;
    position: absolute;
    bottom: 0;
    left: 0;
    display: flex;
    justify-content: space-between;
    color: #FFFFFF;
    padding: 0 4px 1px 4px;
    font-size: 12px;
    box-sizing: border-box;
    background: linear-gradient(0deg, rgba(57, 57, 56, 0.8) 1%, rgba(104, 104, 104, 0) 100%);
}

.videoListStyle > .imgArea > .triangle span {
    position: absolute;
    left: 2px;
    top: 5px;
    z-index: 999;
    font-size: 10px;
    transform: rotate(-32deg);
    color: #fff;
}

.videoListStyle > .imgArea > .triangle {
    position: relative;
}

.videoListStyle > .imgArea > .triangleRed:after {
    border-top: 34px solid #CB266D;
    left: 0;
    content: "";
    border-right: 49px solid transparent;
    position: absolute;
    top: 0;
    z-index: 998;
}

.videoListStyle > .imgArea > .triangleGreen:after {
    border-top: 34px solid #12CC82;
    left: 0;
    content: "";
    border-right: 49px solid transparent;
    position: absolute;
    top: 0;
    z-index: 998;
}

.videoListStyle > .imgArea {
    width: 100%;
    position: relative;
    overflow: hidden;
    box-sizing: border-box;
    border-radius: 5px;
}

.videoListStyle {
    width: 50%;
    height: auto;
    overflow: hidden;
    box-sizing: border-box;
    padding: 5px;
    border-radius: 4px;
}

.videoListBox {
    width: 100%;
    height: auto;
    overflow: hidden;
    box-sizing: border-box;
    background-color: #FFFFFF;
    display: flex;
    align-content: flex-start;
    flex-flow: row wrap;
    padding: 5px;

}

.publicIndexVideoMoudleBox {
    width: 100%;
    height: auto;
    overflow: hidden;
    box-sizing: border-box;
}

.publicIndexVideoMoudleBox > .top {
    padding: 10px 10px 5px 10px;
    box-sizing: border-box;
    width: 100%;
    height: auto;
    background-color: #FFFFFF;
}

.publicIndexVideoMoudleBox > .top > p {
    border-left: 4px solid #CB266D;
    padding-left: 10px;
    color: #404040;
    font-size: 18px;
    font-weight: 800;
}
</style>



