<template>
    <div class="publicAllContentBg" id="publicTopHeaderWithCancel">
        <van-nav-bar
            class="publicAllNavbarClass paddingTop"
            right-text="取消"
            @click-right="returnFn"
        >
            <template #left>
                <van-form @submit="goToSearchPage">
                    <van-field
                        v-model="userInput"
                        left-icon="search"
                        :readonly="returnReadOnly"
                        @click="inputOnClick"
                        :placeholder="returnPlaceholder"
                    ></van-field>
                </van-form>
            </template>
        </van-nav-bar>
        <!--搜索栏-->
        <!--<div class="topInputBox">

        </div>-->
    </div>

</template>

<script>
export default {
    name: "publicTopHeaderWithCancel",
    computed:{
        returnReadOnly(){
            /*if(this.$route.name==="searchResult"){
                return true;
            }*/
            return false;
        },
        returnPlaceholder(){
            if(this.$route.name==="searchResult"||this.$route.name==="searchResultForTag"){
                return this.$store.state.searchInfoSave.info;
            }
            return this.$globalFn.returnPublicPlaceholder();
        }
    },
    data() {
        return {
            /*用户输入*/
            userInput: ""
        }
    },
    methods: {
        /*返回判断*/
        returnFn(){
            console.log('返回');
            this.$router.go(-1);
        },
        /*点击搜索框*/
        inputOnClick(){
            /*如果当前页面是搜索结果页面，那么直接切换搜索页面*/
            /*if(this.$route.name==="searchResult"){
                console.log('replace');
                this.$router.replace({name:'searchPage'});
            }*/
        },
        /*跳转搜索页面*/
        goToSearchPage() {

            /*存储信息*/
            this.$store.commit('changeSearchInfo',{
                type:1,
                info:this.userInput,
                videoType:1,
                sortFn:null
            });
            /*如果当前是搜索结果页面，那么直接触发搜索结果页面的方法，而不是跳转搜索结果*/
            if(this.$route.name==="searchResult"){
                this.$emit('search',null);
                return;
            }
            this.$router.replace({name:"searchResult"});
        }
    }
}
</script>

<style scoped>
.topHeaderBg {
    width: 100%;
    box-sizing: border-box;
    padding: 10px 10px;
    display: flex;
    justify-content: space-between;
}

.topHeaderBg > .left {
    width: 34%;
}

.topHeaderBg > .left img {
    display: block;
    width: 100%;
}

.topHeaderBg > .right {
    width: 64%;
    display: flex;
    align-items: center;
    justify-content: flex-end;
}

.topHeaderBg > .right p {
    color: #A0A4AE;
    font-size: 12px;
}

.publicTopHeaderLine {
    width: 1px;
    background-color: #686c75;
    height: 12px;
    margin: 0 8px;
    display: inline-block;
}

.topInputBox {
    width: 100%;
    box-sizing: border-box;
    padding: 0 10px;
}
</style>



