<template>
    <div class="publicAllContentBg" id="searchResult">
        <publicTopHeaderWithCancel></publicTopHeaderWithCancel>
        <div class="publicNeedXPaddingBg">
            <publicLoadingAnm v-if="!loadingListOptions.firstLoadingDone"></publicLoadingAnm>
            <!--条件排序-->
            <div class="publicSortBox" v-show="loadingListOptions.list.length>0">
                <ul class="publicSortList">
                    <li v-if="index<2" v-for="(item,index) in conditionScreeningOptions.list" :key="index">
                        <div :class="item.active===true?'publicSortListStyle active':'publicSortListStyle'" @click="sortClick(item,index)">
                            <p class="up" v-show="item.sortFn.dir==='asc'">{{item.text}}</p>
                            <p class="down" v-show="item.sortFn.dir==='desc'">{{item.text}}</p>
                        </div>
                    </li>
                </ul>
            </div>
            <!--视频列表-->
            <van-list
                v-if="loadingListOptions.list.length>0&&loadingListOptions.firstLoadingDone"
                v-model="loadingListOptions.loading"
                :finished="loadingListOptions.finished"
                @load="onLoad"
                :immediate-check="false"
                finished-text="没有更多了"
                :offset="100"
            >
                <publicVideoList :videoList="loadingListOptions.list"></publicVideoList>
            </van-list>
            <!--视频为空时的提示-->
            <noResult desc="暂无结果" v-if="loadingListOptions.list.length===0&&loadingListOptions.firstLoadingDone"></noResult>
        </div>

    </div>
</template>

<script>
import noResult from "../../components/noResult";
import publicTopHeaderWithCancel from "../../components/publicTopHeaderWithCancel";
import publicLoadingAnm from "../../components/publicLoadingAnm";
import aesImage from "../../components/aesImage";
import publicVideoList from "../../components/publicVideoListModule";
export default {
    name: 'searchResult',
    components: {
        publicVideoList,
        publicLoadingAnm,
        aesImage,
        publicTopHeaderWithCancel,
        noResult
    },
    computed:{
    },
    methods: {
        onSubmit(){
            console.log('哦牛批');
        },
        /*返回上一页面时的事件*/
        returnLastPage(){
            this.$store.commit('changeSearchInfo',{
                type:1,
                info:"",
                videoType:1,
                sortFn:null
            });
            this.$router.go(-1);
        },
        /*返回搜索条件文字*/
        returnSortFnObj(){
            if(this.$store.state.searchInfoSave.sortFn!=null){
                this.conditionScreeningOptions.request[0]=this.$store.state.searchInfoSave.sortFn;
                console.log("打印this.conditionScreeningOptions.list");
                console.log(this.conditionScreeningOptions.list);
                /*切换选中*/
                for(let obj of this.conditionScreeningOptions.list){
                    /*console.log('固定')
                    console.log(obj.sortFn.column);
                    console.log('传递')
                    console.log(this.conditionScreeningOptions.request[0]);*/
                    if(obj.sortFn.column===this.conditionScreeningOptions.request[0].column){
                        obj.active=true;
                    }
                    else{
                        obj.active=false;
                    }
                }
            }
        },
        /*排序点击*/
        sortClick(item,index){
            console.log(item);
            if(this.loadingListOptions.list.length===0){
                return;
            }
            /*如果当前选项是选中状态，那么就执行相反的排序*/
            if(item.active===true){
                item.sortFn.dir==="desc"?item.sortFn.dir="asc":item.sortFn.dir="desc";
            }
            else if(item.active===false){
                /*清除其余选项的排序/选中状态*/
                for(let obj of this.conditionScreeningOptions.list){
                    obj.sortFn.dir="desc";
                    obj.active=false;
                }
                /*默认选择其他选项都是降序排列*/
                item.active=true;
                item.sortFn.dir="desc";
            }
            /*放置到存储中*/
            this.conditionScreeningOptions.request=[item.sortFn];
            /*重新请求*/
            this.getVideoListFn(0);
        },
        /*获取视频列表的方法*/
        getVideoListFn(type){

            console.log(this.$store.state.searchInfoSave);
            /*
            * type：是否是继续加载，1是0不是
            * */

            console.log("获取视频列表");
            /*如果是重新加载，那么清空数据*/
            if(type===0){
                /*清空数据*/
                this.loadingListOptions.list=[];
                /*清空总页数*/
                this.loadingListOptions.totalPage=0;
                /*清空当前页数*/
                this.loadingListOptions.current=1;
                /*允许继续下拉*/
                this.loadingListOptions.finished=false;
                this.loadingListOptions.firstLoadingDone=false;
            }
            /*请求*/
            this.$requestApi.getVideoList({
                length:this.loadingListOptions.pageSize,
                page:type===1?this.loadingListOptions.current+1:1,
                searchText:this.$store.state.searchInfoSave.type===1?this.$store.state.searchInfoSave.info:undefined,
                type:this.$store.state.searchInfoSave.videoType,
                typeId:this.$store.state.searchInfoSave.type===2?this.$store.state.searchInfoSave.info:undefined,
                orderText:this.conditionScreeningOptions.request
            })
                .then(res=>{
                    /*如果当前广告字段存在且当前是首次请求，那么就放置广告*/
                    if(type===0&&res.data.list.length>0){
                        for(let obj of this.$store.state.adList){
                            if(obj.name==="h5titlepage"){
                                res.data.list.splice(1,1,{
                                    coverImgUrl:obj.imgUrl,
                                    isAd:true,
                                    name:obj.title
                                });
                                break;
                            }
                        }
                    }
                    /*放置视频数据*/
                    /*for(let obj of res.data.list){
                        obj.img=require('../../assets/indexBanner.jpg');
                    }*/

                    type===0?this.loadingListOptions.list=res.data.list:this.loadingListOptions.list=this.loadingListOptions.list.concat(res.data.list);
                    /*放置总页数*/
                    this.loadingListOptions.totalPage=this.$globalFn.totalPageCountFn(this.loadingListOptions.pageSize,res.data.recordsTotal);
                    /*放置当前页数*/
                    this.loadingListOptions.current=res.data.page;
                    /*记录总条数*/
                    this.loadingListOptions.totalCount=res.data.recordsTotal;
                    /*console.log("打印当前页数");
                    console.log(this.loadingListOptions.current);
                    console.log("打印当前总页数");
                    console.log(this.loadingListOptions.totalPage);*/
                    /*判断是否已经到最后一页，如果是，那么就停止下拉加载*/
                    if(this.loadingListOptions.totalPage===this.loadingListOptions.current){
                        this.loadingListOptions.finished=true;
                    }
                    else if(this.loadingListOptions.totalPage===1){
                        this.loadingListOptions.finished=true;
                    }
                    /*关闭加载动画*/
                    this.loadingListOptions.loading=false;
                    this.loadingListOptions.firstLoadingDone=true;
                })
                .catch(err=>{

                })
        },
        /*继续加载*/
        onLoad(){
            console.log("触底加载");
            /*请求视频列表*/
            this.getVideoListFn(1);
            // console.log(this.loadingListOptions);
        },
        /*初始化数据*/
        getFirstData(){
            /*执行排序方法初始化*/
            this.returnSortFnObj();
            /*放置关键字*/
            this.$store.state.searchInfoSave.type===1?this.inputOptions.val=this.$store.state.searchInfoSave.info:null;
            this.getVideoListFn(0);
        },
        /*清空数据*/
        resetData(){
            this.loadingListOptions.list=[];
            this.inputOptions.val="";
        }
    },
    data() {
        return {
            /*顶部搜索框右侧图标*/
            topInputRightIcon:require('../../assets/navInputRightIcon.png'),
            /*顶部logo*/
            topLogo: require('../../assets/logo.png'),
            /*搜索栏配置*/
            inputOptions: {
                /*搜索值*/
                val: ""
            },
            /*加载更多配置*/
            loadingListOptions:{
                /*第一次加载*/
                firstLoadingDone:false,
                /*是否在加载*/
                loading:false,
                /*是否已经结束*/
                finished:false,
                /*列表*/
                list:[],
                /*当前页数*/
                current:1,
                /*总页数*/
                totalPage:0,
                /*总条数*/
                totalCount:0,
                /*每页条数*/
                pageSize:20
            },
            /*条件筛选配置*/
            conditionScreeningOptions:{
                /*传递给后端的数组(默认传递这个)*/
                request:[
                    {
                        dir: "desc",
                        column: "addTimeStamp"
                    }
                ],
                /*列表*/
                list:[
                    {
                        id:1,
                        text:"最新",
                        /*0升序，1降序*/
                        sort:1,
                        /*是否选中*/
                        active:true,
                        /*对应的排序方法*/
                        sortFn:{
                            dir:"desc",
                            column: "addTimeStamp"
                        }
                    },
                    {
                        id:2,
                        text:"点赞",
                        /*0升序，1降序*/
                        sort:1,
                        /*是否选中*/
                        active:false,
                        /*对应的排序方法*/
                        sortFn:{
                            dir:"desc",
                            column: "likeCount"
                        }
                    },
                    {
                        id:3,
                        text:"播放",
                        /*0升序，1降序*/
                        sort:1,
                        /*是否选中*/
                        active:false,
                        /*对应的排序方法*/
                        sortFn:{
                            dir:"desc",
                            column: "seeCount"
                        }
                    },
                    {
                        id:4,
                        text:"收藏",
                        /*0升序，1降序*/
                        sort:1,
                        /*是否选中*/
                        active:false,
                        /*对应的排序方法*/
                        sortFn:{
                            dir:"desc",
                            column: "collectionCount"
                        }
                    }
                ]
            }
        }
    },
    created() {
        this.getFirstData();
    }
    /*beforeRouteEnter(to, from, next) {
        console.log(to);
        console.log(from);
        //如果是来自分类，那么就不需要启用旧数据，重新读取
        if (from.name === "menu") {
            to.meta.useOldData = false;
        }
        else if (from.name === "videoContent") {
            //如果是来自商品详情,那么就启用旧数据
            to.meta.useOldData = false;
        }
        next();
    },
    activated() {
        console.log('激活');
        if (this.$route.meta.useOldData === false) {
            //请求新数据
            console.log("重新请求数据");
            this.loadingListOptions.finished = false;
            /!*重置排序*!/
            this.conditionScreeningOptions.request=[{
                dir: "desc",
                column: "addTimeStamp"
            }];
            for(let obj of this.conditionScreeningOptions.list){
                obj.sortFn.dir="desc";
                obj.active=false;
            }
            this.conditionScreeningOptions.list[0].active=true;
            this.getVideoListFn(0);
        }
        //重置防止出错
        this.$route.meta.useOldData = false;
    }*/
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
/*二级分类样式*/
.lv2NavBox{
    width: 100%;
    height: auto;
    overflow: hidden;
    box-sizing: border-box;
    background-color: #f7f7f7;
    padding: 10px 10px 0 10px;
}
.lv2NavBox>.list{
    width: 100%;
    height: auto;
    overflow: hidden;
    box-sizing: border-box;
}
.lv2NavBox>.list>li{
    float: left;
    padding-right: 10px;
    padding-bottom: 10px;
}
.lv2NavBox>.list>li>p{
    padding: 4px 8px;
    color: #646464;
    border-radius: 50px;
    background-color: #fff;
    border: 1px solid #b2b2b2;
    font-size: 14px;
}
.lv2NavBox>.list>li>p.active{
    padding: 4px 8px;
    color: #fff;
    border-radius: 50px;
    background-color: #cb266d;
    border: 1px solid #cb266d;
    font-size: 14px;
}
.indexIconNavBox {
    width: 100%;
    height: auto;
    overflow: hidden;
    box-sizing: border-box;
    padding-top: 10px;
    background-color: #FFFFFF;
}


/*顶部五个图标导航栏样式*/
.indexIconNavList {
    width: 100%;
    height: auto;
    overflow: hidden;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    display: flex;
    /*justify-content: space-between;*/
    flex-wrap: wrap;
    padding: 14px 0;
    background-color: #FFFFFF;
    margin-bottom: 10px;
}

.indexIconNavList > li {
    height: auto;
    overflow: hidden;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    padding: 0 10px;
    width: 20%;
}

.indexIconNavListStyle {
    width: 100%;
    height: auto;
    overflow: hidden;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
}
.indexIconNavListStyle>div.line{
    width: 100%;
    height: 10px;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: flex-end;
}
.indexIconNavListStyle>div.line>div{
    width: 50%;
    height: 2px;
    background-color: #CB266D;
}
.indexIconNavListStyle p.text {
    width: 100%;
    height: auto;
    overflow: hidden;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    text-align: center;
    color: #646464;
    font-size: 14px;
}
.indexIconNavListStyle p.textActive {
    width: 100%;
    height: auto;
    overflow: hidden;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    text-align: center;
    color: #CB266D;
    font-size: 14px;
}
.indexIconNavListStyle div.icon {
    width: 80%;
    padding-top: 80%;
    border-radius: 50%;
    height: auto;
    overflow: hidden;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    margin: 0 auto 4px auto;
    position: relative;
}

.indexIconNavListStyle img {
    display: block;
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
}
</style>



