                    </div>
                </div>-->
            </div>
            <!--尚未登录且视频不是免费视频-->
            <div class="unLoginBox"
                 v-if="showLoginStatus">
                <p class="textLine">免费注册 精彩视频免费看</p>
                <p class="textLine">赶快加入 体验更多精彩</p>
                <!--<van-button round
                            @click="$router.push({name:'login'})"
