                <p  @click="$router.push({name:'login'})" class="linkOutButton vip">登录/注册</p>
            </div>
        </div>
        <!--权限判断界面-->
        <div class="userStatusBg" v-if="nowVideoStep===1">
            <!--已经登录但视频需要vip且用户不是vip(vip优先，只有开通了vip，才可以观看视频)-->
            <div class="unLoginBox"
                 v-if="showNeedVipBg">
                <!--当前试看尚未结束前显示-->
                <!--<p class="textLine" v-if="videoStatus.tryWatchVideoIsPlayDone===false">开通<span>VIP</span>会员可享受以下权限</p>-->
                <p class="textLine" v-if="videoStatus.tryWatchVideoIsPlayDone===false">会员免广告，极速秒播</p>
                <p class="textLine" v-if="videoStatus.tryWatchVideoIsPlayDone===false">海量视频任意看</p>
                <div class="buttonLine">
                    <p @click="goRecharge(1)" class="linkOutButton vip">
                        <van-icon :name="require('../../assets/icon_members_nor@2x.png')"/>
                        去开通VIP</p>
                    <!--<a href="javascript:void(0)" @click="tryWatchVideoClick" class="linkOutButton try"
                       v-if="videoStatus.tryWatchVideoIsPlayDone===false">
                        <van-icon name="play-circle-o"/>
                        我要试看</a>-->
                </div>
            </div>
            <!--已经登录且已经开通vip但视频需要G点且尚未购买该视频-->
            <div class="unLoginBox"
                 v-if="showNeedGPointBg">
                <!--如果用户G点大于当前所需G点，显示如下-->
                <!--<p class="textLine"><span>VIP会员</span>专享，独家视频</p>
                <p class="textLine">G点视频更精彩！</p>-->
                <div class="buttonLine">
                    <!--点击立即播放，判断当前G余额，然后弹窗-->
                    <a href="javascript:void(0);" class="linkOutButton vip" @click="beforeGVideoPlay">
                        <van-icon :name="require('../../assets/icon_diamond_video.png')"/>
                        {{videoContentOptions.point}}钻石解锁该视频</a>
                    <!--<a href="javascript:void(0);" class="linkOutButton try" @click="tryWatchVideoClick"
                       v-if="videoStatus.tryWatchVideoIsPlayDone===false">
                        <van-icon name="play-circle-o"/>
                        我要试看</a>-->
                </div>
                <!--如果用户G点小于所需G点，显示如下-->
                <!--<p class="textLine" v-if="!showGPointPlayNow">G点消耗提醒</p>
                <p class="textLine" v-if="!showGPointPlayNow">余额不足，充值G点解锁即可观看。</p>
                <div class="buttonLine" v-if="!showGPointPlayNow">
                    &lt;!&ndash;点击立即播放，判断当前G余额，然后弹窗&ndash;&gt;
                    <a :href="$globalFn.returnRechargeURL()" class="linkOutButton vip" target="_blank">
&lt;!&ndash;                        <van-icon name="play-circle-o"/>&ndash;&gt;
                        去充值</a>
                    <a href="javascript:void(0);" class="linkOutButton try">
&lt;!&ndash;                        <van-icon name="play-circle-o"/>&ndash;&gt;
                        取消</a>
                </div>-->
            </div>
            <!--已经播放完正式视频，进入分享页面-->
            <div class="unLoginBox"
                 v-if="showShareBg">
                <div class="buttonLine">
                    <!--重新播放页面-->
                    <a href="javascript:void(0);" class="linkOutButton replay" @click="replayVideo">
                        <van-icon name="replay"/>
                        重播</a>
                </div>
            </div>
            <!--已经开通vip且G点充足-->
            <!--<div class="unLoginBox"
                 v-if="$store.state.isLogin===true&&$store.state.userInfo.isVip===1&&videoContentOptions.point>0&&$store.state.userInfo.point>=videoContentOptions.point">
                <p class="textLine">是否使用G点购买该视频？</p>
                <van-button
                    round
                    type="info"
                    :hairline="false"
                    style="background-color: #CB266D"
                    @click="buyVideo"
                >使用G点购买
                </van-button>
            </div>-->
            <!--底图-->
            <div class="backImg">
                <aesImage :file-url="imgCoverOutside" :primordial-img="true"
                          v-if="imgCoverOutside!==''"></aesImage>
                <!--<img :src="playerOptions.cover" alt="">-->
            </div>
        </div>
        <!--正式视频控件-->
        <!--<div :class="videoStatus.isPlay===true?'videoBigBg fixed':'videoBigBg'" v-show="nowVideoStep===2">-->
        <div :class="videoStatus.isPlay===true?'videoBigBg fixed':'videoBigBg'" v-if="nowVideoStep===2">
            <xgPlayer :id="'formatVideo'"
                      :url="formatUrl"
                      @videoOnClick="videoOnClick"
                      @videoOnPause="videoOnPause"
                      @officialVideoPlayDone="officialVideoPlayDone"
            ></xgPlayer>
            <!--            <div id="formatVideo"></div>-->
        </div>
        <!--暂停时的占位容器(只在有视频播放时渲染，进行占位)-->
        <xgPlayer
            v-show="videoStatus.isPlay===true&&nowVideoStep===2"
            :id="'formatVideoCopy'"
            :url="''"
        ></xgPlayer>
        <!--        <div id="formatVideoCopy" v-show="videoStatus.isPlay===true&&nowVideoStep===2"></div>-->
        <!--试看播放器-->
        <xgPlayer
            v-if="showTryWatchPlayer&&nowVideoStep===3"
            :id="'tryVideo'"
            :url="tryVideoUrl"
            @ended="tryWatchVideoEnded"
        ></xgPlayer>
        <!--        <div id="tryVideo" v-show="showTryWatchPlayer&&nowVideoStep===3"></div>-->
        <!--视频简介-->
        <!--<van-collapse v-model="collapseOptions.active" :border="false" style="margin-bottom: 10px">
            <van-collapse-item name="1">
                <template #title>
                    <div>{{videoContentOptions.name}}&lt;!&ndash;尚未收藏&ndash;&gt;
                        <van-icon name="like-o" v-if="videoContentOptions.isCollected===0"
                                  @click="clickCollectButton(1)"/>
                        &lt;!&ndash;已经收藏&ndash;&gt;
                        <van-icon name="like" v-if="videoContentOptions.isCollected===1"
                                  @click="clickCollectButton(0)" /></div>

                </template>
                <div class="videoInfoLineOneTwo">
                    <p>{{ videoContentOptions.videoIntro }}</p>
                </div>
            </van-collapse-item>
        </van-collapse>-->
        <!--视频标题-->
        <div class="publicNeedXPaddingBg marginTop">
            <div class="videoTitle">
                <p>{{videoContentOptions.name}}</p>
                <!--收藏按钮-->
                <div class="collect" v-if="videoContentOptions.isCollected===0">
                    <van-icon name="like-o"
                              @click="clickCollectButton(1)"></van-icon>
<!--                    <p>点击收藏</p>-->
                </div>
                <div class="collect"  v-if="videoContentOptions.isCollected===1">
                    <van-icon name="like"
                              color="#F54B64"
                              @click="clickCollectButton(0)" />
                    <!--<p>取消收藏</p>-->
                </div>
            </div>

            <!--功能栏-->
            <div class="videoInfoLineOneTwo">
                <p>{{ videoContentOptions.videoIntro }}</p>
            </div>
            <div class="videoInfoLineOne">
                <p>{{ videoContentOptions.playTimes }}次播放</p>
                <p>{{ videoContentOptions.createTime }}</p>
            </div>
            <!--视频标签-->
            <div class="videoSignBox">
                <!--            <div class="line"></div>-->
                <ul>
                    <li v-for="(item,index) in videoContentOptions.tags" :key="index">
                        <p @click="goSearchList(item)">{{ item }}</p>
                    </li>
                </ul>
            </div>
        </div>


        <!--视频操作-->
       <!-- <ul class="videoFunctionList">
            <li>
                &lt;!&ndash;尚未点赞 点赞数&ndash;&gt;
                <div class="style" @click="clickGoodButton(1)" v-if="videoContentOptions.isGood===0">
                    <div>
                        <van-icon name="good-job" size="20px" color="#979797"/>
                    </div>
                    <p>{{ videoContentOptions.goodCount }}</p>
                </div>
                &lt;!&ndash;已经点赞 点赞数&ndash;&gt;
                <div class="style" v-if="videoContentOptions.isGood===1" @click="clickGoodButton(0)">
                    <div>
                        <van-icon name="good-job" size="20px" color="#cb266d"/>
                    </div>
                    <p class="active">{{ videoContentOptions.goodCount }}</p>
                </div>
            </li>
            <li>
                &lt;!&ndash;尚未收藏 收藏数&ndash;&gt;
                <div class="style" v-if="videoContentOptions.isCollected===0" @click="clickCollectButton(1)">
                    <div>
                        <van-icon name="star" size="20px" color="#979797"/>
                    </div>
                    &lt;!&ndash;<p>{{videoContentOptions.collection}}</p>&ndash;&gt;
                    <p>收藏</p>
                </div>
                &lt;!&ndash;已经收藏 收藏数&ndash;&gt;
                <div class="style" v-if="videoContentOptions.isCollected===1" @click="clickCollectButton(0)">
                    <div>
                        <van-icon name="star" size="20px" color="#cb266d"/>
                    </div>
                    &lt;!&ndash;<p class="active">{{videoContentOptions.collection}}</p>&ndash;&gt;
                    <p class="active">已收藏</p>
                </div>
            </li>
            <li>
                &lt;!&ndash;缓存&ndash;&gt;
                <div class="style" @click="$toast.fail('请下载APP体验')">
                    <div>
                        <van-icon name="down" size="20px" color="#979797"/>
                    </div>
                    <p>缓存</p>
                </div>
            </li>
            <li>
                &lt;!&ndash;分享&ndash;&gt;
                <div class="style" @click="clickShareButton">
                    <div>
                        <van-icon name="share" size="20px" color="#979797"/>
                    </div>
                    <p>分享</p>
                </div>
            </li>
        </ul>-->
        <!--广告位-->
        <!--<a class="adBox" v-if="this.adOptions.bottom!==null" :href="this.adOptions.bottom.linkUrl" target="_blank">
            <img :src="this.adOptions.bottom.imgUrl" alt="">
        </a>-->
        <div class="publicNeedXPaddingBg">
            <!--猜你喜欢-->
            <guassYourLike></guassYourLike>
        </div>

    </div>
</template>

<script>
import aesImage from "../../components/aesImage";
import guassYourLike from "../../components/guassYourLike";
import xgPlayer from "../../components/xgPlayer";

export default {
    name: 'videoContent',
    components: {
        guassYourLike,
        aesImage,
        xgPlayer
    },
    computed: {
        /*是否显示G点立即播放*/
        showGPointPlayNow() {
            if (this.$store.state.userInfo.point >= this.videoContentOptions.point) {
                return true;
            }
            return false;
        },
        /*是否显示分享*/
        showShareBg() {
            /*如果正式视频已经播放结束并且当前播放状态停止*/
            if (this.videoStatus.isVideoPlayDone === true && this.videoStatus.isPlay === false) {
                return true;
            }
            return false;
        },
        /*是否显示Vip拦截*/
        showNeedVipBg() {
            if (this.$store.state.isLogin === false) {
                return false;
            }
            /*如果是vip不显示*/
            if (this.$store.state.userInfo.isVip === 1) {
                return false;
            }
            if (this.videoContentOptions.isVip === 1) {
                /*已经登录且当前视频需要vip*/
                return true;
            }
            return false;

        },
        /*是否显示G点拦截*/
        showNeedGPointBg() {
            if (this.videoContentOptions.isBuy === 1) {
                /*如果已经购买，直接返回false*/
                return false;
            }
            if (this.$store.state.isLogin === true && this.$store.state.userInfo.isVip === 1 && this.videoContentOptions.point > 0) {
                /*已登录 是vip 当前视频需要G点购买*/
                return true;
            }
            return false;
        },
        /*是否显示试看视频播放器*/
        showTryWatchPlayer() {
            /*如果试看视频已经播放完毕，直接不显示*/
            if (this.videoStatus.tryWatchVideoIsPlayDone) {
                return false;
            }
            /*如果已经准备好渲染且当前模式是播放试看视频,那么显示*/
            if (this.videoStatus.tryWatchVideoIsReady === true && this.videoStatus.isTryWatchVideo === true) {
                return true;
            }
            return false;
        },
        /*是否显示去登录注册*/
        showLoginStatus() {
            /*如果当前视频是免费视频，不显示*/
            if (this.videoContentOptions.isVip === 0 && this.videoContentOptions.point === 0) {
                // console.log('如果当前视频是免费视频，不显示');
                return false;
            }
            /*如果当前尚未登录且视频需要vip，那么显示*/
            if (this.$store.state.isLogin === false && this.videoContentOptions.isVip === 1) {
                // console.log('如果当前尚未登录且视频需要vip，那么显示');
                return true;
            }
            /*如果当前尚未登录且视频需要g点，那么显示*/
            if (this.$store.state.isLogin === false && this.videoContentOptions.point > 0) {
                // console.log('如果当前尚未登录且视频需要g点，那么显示');
                return true;
            }
        }
    },
    methods: {
        /*去充值vip*/
        goRecharge(type){
            /*
            * type1:充值vip 2:充值钻石
            * */
            this.$router.push({name:"rechargeIndex"});
        },
        /*点击分享按钮*/
        clickShareButton() {
            if (this.$store.state.isLogin === false) {
                this.$router.push({name: "login"});
                return;
            }
            /*loading*/
            this.$toast.loading({
                duration:0,
                message:'读取中'
            });
            /*请求分享链接*/
            this.$requestApi.getShareList({
                userId: this.$store.state.userInfo.userId,
                type: 1,
                videoId: this.$route.params.id
            })
                .then(res => {
                    console.log(res);
                    this.shareOptions.linkList.QQShortUrl = res.data.QQShortUrl;
                    this.shareOptions.linkList.webShortUrl = res.data.webShortUrl;
                    this.shareOptions.linkList.wechatShortUrl = res.data.wechatShortUrl;
                    /*关闭loading*/
                    this.$toast.clear();
                    /*弹出*/
                    this.shareOptions.show=true;
                })
                .catch(err => {

                })
        },
        /*点击了假的播放按钮第一次播放*/
        firstStartPlay() {
            /*先判断是否满足播放条件，如果不满足，进入权限显示*/
            if (this.videoPlayEnoughFn() === true) {
                /*直接进入开始播放*/
                this.nowVideoStep = 2;
                return;
            }
            /*进入权限判断*/
            this.nowVideoStep = 1;
            /*第一次播放开始*/
            // this.videoStatus.firstPlayIsStart=true;
        },
        /*重新播放正式视频*/
        replayVideo() {
            /*重置视频播放完成状态*/
            this.videoStatus.isVideoPlayDone = false;
            /*改变正式视频播放状态*/
            this.videoStatus.isPlay = false;
            /*显示正式视频*/
            this.nowVideoStep = 2;
        },
        /*正式视频播放结束*/
        officialVideoPlayDone() {
            console.log('播放结束');
            /*需要启动*/
            this.videoStatus.isVideoPlayDone = true;
            /*改变正式视频播放状态*/
            this.videoStatus.isPlay = false;
            /*返回视频权限判断页面*/
            this.nowVideoStep = 1;
        },
        /*G点视频播放前的点击*/
        beforeGVideoPlay() {
            /*如果当前G点足够，那么直接购买*/
            if (this.showGPointPlayNow === true) {
                this.$dialog.confirm({
                    title: '钻石消耗提醒',
                    message: '是否消耗' + this.videoContentOptions.point + "钻石解锁该视频？"
                })
                    .then(() => {
                        /*检查G点是否足够，然后执行操作*/
                        if (this.$store.state.userInfo.point >= this.videoContentOptions.point) {
                            this.buyVideo();
                        } else {
                            /*提示G点不足，去充值*/
                            this.$toast.fail('G点余额不足，请充值！');
                        }
                    })
                    .catch(() => {
                        // on cancel
                    });
            } else {
                this.$dialog.confirm({
                    title: '钻石消耗提醒',
                    message: '钻石不足，充值钻石解锁即可观看',
                    confirmButtonText:"去充值"
                })
                    .then(() => {
                        /*跳转充值页面*/
                        this.$router.push({name:'rechargeIndex'});
                    })
                    .catch(() => {
                        // on cancel
                    });
            }

        },
        /*试看视频播放完毕*/
        tryWatchVideoEnded() {
            console.log('试看结束');
            /*试看视频播放结束*/
            this.videoStatus.tryWatchVideoIsPlayDone = true;
            /*继续显示权限模块*/
            this.nowVideoStep = 1;
        },
        /*点击我要试看*/
        tryWatchVideoClick() {
            this.$toast.loading({
                duration: 0,
                mask: true
            });
            /*获取试看地址*/
            this.getPreVideoUrl();
        },
        /*去搜索*/
        goSearchList(item) {
            console.log(item);
            /*存储信息*/
            this.$store.commit('changeSearchInfo', {
                type: 1,
                info: item,
                videoType: 1,
                sortFn: null
            });
            this.$router.push({name:'searchResult'});
            /*this.$router.push({name: "searchResultForVideoTag", params: {name: item}});*/
        },
        /*是否满足播放条件且进行下一步操作*/
        videoPlayEnoughFn() {
            console.log('进入判断');
            /*如果不是vip，且视频不需要vip，且不需要付费，无需登录,直接显示(免费视频)*/
            if (this.videoContentOptions.isVip === 0 && this.videoContentOptions.point === 0) {
                console.log('如果不是vip，且视频不需要vip，且不需要付费，无需登录,直接显示(免费视频)');
                return true;
            }
            /*如果已经购买了视频，直接显示*/
            if (this.$store.state.isLogin === true && this.videoContentOptions.isBuy === 1) {
                console.log('如果已经购买了视频，直接显示');
                return true;
            }
            /*如果是vip了，并且视频所需G点等于0*/
            if (this.$store.state.isLogin === true && this.$store.state.userInfo.isVip === 1 && this.videoContentOptions.point === 0) {
                console.log('已经登录且是vip了，并且视频所需G点等于0');
                return true;
            }
            /*如果是vip了且G点余额大于视频所需，显示*/
            if (this.$store.state.isLogin === true && this.$store.state.userInfo.isVip === 1 && this.$store.state.userInfo.point > this.videoContentOptions.point === 0) {
                console.log('已经登录且是vip了，是vip了且G点余额大于视频所需');
                return true;
            }
            return false;
        },
        /*购买视频*/
        buyVideo() {
            /*开始loading*/
            this.$toast.loading({
                overlay: true,
                duration: 0,
                message: "解锁中"
            });
            this.$requestApi.useGPointBuyVideo({
                videoId: this.$route.params.id
            })
                .then(res => {
                    this.$toast.success('购买成功');
                    /*改变当前视频是否已经购买*/
                    this.videoContentOptions.isBuy = 1;
                    /*改变G点余额*/
                    this.$store.commit('changeUserInfoOneKey', {
                        key: "point",
                        val: res.data.point
                    });
                    /*请求正式视频地址且切换到正式视频播放*/
                    /*this.getVideoUrl();*/
                    this.nowVideoStep = 2;

                })
                .catch(err => {
                    /*this.$toast.fail(err.message);*/
                })
        },
        /*播放器暂停*/
        videoOnPause() {
            /*是否在播放*/
            this.videoStatus.isPlay = !this.videoStatus.isPlay;
            /*隐藏导航栏*/
            /*this.showTopNavbar = true;*/
        },
        /*播放器被点击*/
        videoOnClick(e) {
            // e.requestFullscreen();
            /*是否在播放*/
            this.videoStatus.isPlay = !this.videoStatus.isPlay;
            console.log('打印操作后的状态');
            console.log(this.videoStatus.isPlay);
            /*隐藏导航栏*/
            /*this.showTopNavbar = false;*/
        },
        /*判断当前请求哪种视频播放url*/
        returnRequestVideoType() {
            /*
            * return 0:免费视频 1:G点视频 2:vip视频
            * */
            if (this.videoContentOptions.isVip === 1) {
                return 2;
            } else if (this.videoContentOptions.isVip === 0 && this.videoContentOptions.point === 0) {
                return 0;
            } else if (this.videoContentOptions.point > 0) {
                return 1;
            }
        },
        /*获取当前视频播放地址的方法*/
        getVideoUrl(type) {
            this.$requestApi.getVideoUrl({
                videoId: this.$route.params.id
            })
                .then(res => {
                    this.playerOptions.source = res.data.url;
                    /*视频准备完成*/
                    this.isVideoAllFunctuonReady = true;
                    /*放置正式url*/
                    this.formatUrl = res.data.url;
                    /*进入到播放步骤(渲染)*/
                    /*this.nowVideoStep = 2;*/
                })
                .catch(err => {
                    console.log(err);
                });
            /*switch (type) {
                /!*获取免费视频*!/
                case 0:
                    this.$requestApi.getFreeVideoUrl({
                        videoId: this.$route.params.id
                    })
                        .then(res => {
                            console.log("获取免费视频url")
                            console.log(res);
                            this.playerOptions.source = res.data.url;
                            /!*视频准备完成*!/
                            this.isVideoAllFunctuonReady = true;
                            /!*执行视频操作*!/
                            this.videoPlayEnoughFn();
                        })
                        .catch(err => {
                            console.log(err);
                        });
                    break;
                case 1:
                    /!*获取收费视频*!/
                    this.$requestApi.getVideoUrl({
                        videoId: this.$route.params.id,
                    })
                        .then(res => {
                            console.log("获取收费视频url")
                            console.log(res);
                            this.playerOptions.source = res.data.url;
                            /!*视频准备完成*!/
                            this.isVideoAllFunctuonReady = true;
                            /!*执行视频操作*!/
                            this.videoPlayEnoughFn();
                        })
                        .catch(err => {
                            console.log(err);
                        });
                    break;
                case 2:
                    /!*获取vip视频*!/
                    this.$requestApi.getVideoUrl({
                        videoId: this.$route.params.id
                    })
                        .then(res => {
                            console.log("获取收费视频url")
                            console.log(res);
                            this.playerOptions.source = res.data.url;
                            /!*视频准备完成*!/
                            this.isVideoAllFunctuonReady = true;
                            /!*执行视频操作*!/
                            this.videoPlayEnoughFn();
                        })
                        .catch(err => {
                            console.log(err);
                        });
                    break;
            }*/
        },
        /*点击收藏按钮*/
        clickCollectButton(type) {
            console.log("打印type");
            console.log(type);
            /*type：0取消收藏操作 1收藏操作*/
            switch (type) {
                case 0:
                    this.$requestApi.userCancelCollectVideo({
                        videoIds: [this.$route.params.id]
                    })
                        .then(res => {
                            /*操作成功提示*/
                            this.$toast('操作成功');
                            /*减少数量*/
                            this.videoContentOptions.collection -= 1;
                            /*变化状态*/
                            this.videoContentOptions.isCollected = 0;
                        })
                        .catch(err => {
                            /*操作成功提示*/
                            this.$toast('操作失败');
                        })
                    break;
                case 1:
                    this.$requestApi.userCollectVideo({
                        videoId: this.$route.params.id
                    })
                        .then(res => {
                            /*操作成功提示*/
                            this.$toast('操作成功');
                            /*增加数量*/
                            this.videoContentOptions.collection += 1;
                            /*变化状态*/
                            this.videoContentOptions.isCollected = 1;
                        })
                        .catch(err => {
                            /*操作成功提示*/
                            this.$toast('操作失败');
                        })

                    break;
            }
        },
        /*返回*/
        onClickLeft() {
            /*回到步骤0*/
            this.$router.go(-1);
        },
        onClickRight() {

        },
        /*获取试看地址*/
        getPreVideoUrl() {
            this.$requestApi.getTryVideoUrl({
                videoId: this.$route.params.id
            })
                .then(res => {
                    console.log("获取试看视频url")
                    console.log(res);
                    this.tryVideoUrl = res.data.preUrl;
                    /*视频准备完成*/
                    this.videoStatus.tryWatchVideoIsReady = true;
                    /*显示试看视频*/
                    this.videoStatus.isTryWatchVideo = true;
                    /*改变步骤*/
                    this.nowVideoStep = 3;
                    /*关闭loading*/
                    this.$toast.clear();
                })
                .catch(err => {
                    console.log(err);
                });
        },
        /*转换aes图片*/
        getAESImgBase64Code(file) {
            this.$axios({
                url: file
            })
                .then(res => {
                    this.playerOptions.cover = this.$globalFn.decryptAESFn(res.data).replace(/\s*/g, "");
                    /*如果当前视频需要登录且需要vip且用户不是vip，那么直接到权限判断*/
                    if(this.$store.state.isLogin===true&&this.videoContentOptions.isVip===1&&this.$store.state.userInfo.isVip===0){
                        this.nowVideoStep=1;
                        return;
                    }
                    /*可以到0屏了*/
                    this.nowVideoStep = 0;
                    /*console.log('封面结束');
                    console.log(this.playerOptions.cover);*/
                })
                .catch(err => {

                })
        },
        /*获取广告*/
        getAdFn(){
            this.$requestApi.getAdList({
                type:1
            })
            .then(res=>{
                for(let obj of res.data.list){
                    /*this.adOptions.bottom=obj;*/
                    if(obj.name==='h5video'){
                        this.adOptions.bottom=obj;
                        break;
                    }
                }
            })
            .catch(err=>{

            })
        },
        /*初始化数据*/
        getFirstData() {
            this.$requestApi.getVideoContent({
                videoId: this.$route.params.id
            })
                .then(res => {
                    let _this = this;
                    /*放置视频封面(给外部权限判断)*/
                    this.imgCoverOutside = res.data.info.coverImgUrl;
                    /*放置是否是vip视频*/
                    this.videoContentOptions.isVip = res.data.info.isVip;
                    /*放置需要的G点*/
                    this.videoContentOptions.point = res.data.info.point;
                    /*是否已经购买*/
                    this.videoContentOptions.isBuy = res.data.info.isBuy;
                    /*放置时长*/
                    this.videoContentOptions.length = res.data.info.length;
                    /*使用async await解决获取视频封面延时问题*/
                    (async function () {
                        /*放置视频封面(给播放器)*/
                        await _this.getAESImgBase64Code(res.data.info.coverImgUrl);
                        /*获取播放地址*/
                        await _this.getVideoUrl(_this.returnRequestVideoType());
                    })();
                    /*放置视频标签*/
                    this.videoContentOptions.tags = res.data.info.tags.split(',');
                    /*放置剧照*/
                    this.videoContentOptions.posterImgList = res.data.info.posterImgUrl;
                    /*放置发布时间*/
                    this.videoContentOptions.createTime = res.data.info.addTime;
                    /*放置收藏数*/
                    this.videoContentOptions.collection = res.data.info.collectionCount;
                    /*放置标题*/
                    this.videoContentOptions.name = res.data.info.name;
                    /*放置播放次数*/
                    this.videoContentOptions.playTimes = res.data.info.seeCount;
                    /*视频简介*/
                    this.videoContentOptions.videoIntro = res.data.info.description === "" ? '暂无简介' : res.data.info.description;
                    /*点赞数量*/
                    this.videoContentOptions.goodCount = res.data.info.likeCount;
                    /*添加历史记录*/
                    /*this.$globalFn.addVideoHistory({
                        videoId: this.$route.params.id,
                        name: res.data.info.name,
                        active: false,
                        needVip: res.data.info.isVip,
                        addTime: this.$globalFn.getTimeStringForHistory(),
                        coverImgUrl: res.data.info.coverImgUrl
                    }, this.$store.state.videoHistoryList);*/
                    /*如果已经登录，放置是否点赞和收藏*/
                    if (this.$store.state.isLogin === true) {
                        /*是否点赞*/
                        this.videoContentOptions.isGood = res.data.info.isLike;
                        /*是否收藏*/
                        this.videoContentOptions.isCollected = res.data.info.isCollect;
                        /*判断非vip登录且当前视频需要vip，那么直接跳转到步骤1*/
                        if(this.videoContentOptions.isVip===1&&this.$store.state.userInfo.isVip===0){
                            this.nowVideoStep=1;
                        }
                    }
                })
                .catch(err => {

                })
        },
        /*重置视频状态*/
        resetVideoStatus() {
            /*销毁播放器*/


            /*if (this.formatVideoObj !== null) {
                this.formatVideoObj.destroy(false);
            }
            if (this.tryVideoObj !== null) {
                this.tryVideoObj.destroy(false);
            }
            if (this.formatVideoCopyObj !== null) {
                this.formatVideoCopyObj.destroy(false);
            }*/
            /*重置视频状态*/
            for (let key in this.videoStatus) {
                this.videoStatus[key] = false;
            }
            /*重置是否可以渲染*/
            this.isVideoAllFunctuonReady = false;
            /*回复步骤*/
            this.nowVideoStep = -1;
        }
    },
    data() {
        return {
            /*页面广告*/
            adOptions:{
                /*猜你喜欢顶部*/
                bottom:null
            },
            /*当前进入视频页面阶段
            * 0:只显示封面和假的播放按钮
            * 1.进入权限判断
            * 2.正式视频播放
            * 3.试看视频
            * */
            nowVideoStep: -1,
            /*外部封面储存地址*/
            imgCoverOutside: "",
            /*当前视频状态*/
            videoStatus: {
                /*第一次播放是否已经开始*/
                firstPlayIsStart: false,
                /*正式视频是否在播放*/
                isPlay: false,
                /*是否全屏*/
                isFullscreen: false,
                /*正式视频是否已经播放完毕*/
                isVideoPlayDone: false,
                /*是否是试看状态*/
                isTryWatchVideo: false,
                /*试看视频是否在播放*/
                tryWatchVideoIsPlay: false,
                /*试看视频是否已经准备好*/
                tryWatchVideoIsReady: false,
                /*试看视频是否已经播放完毕*/
                tryWatchVideoIsPlayDone: false
            },
            /*是否显示顶部导航栏*/
            showTopNavbar: true,
            /*视频是否准备就绪可以渲染*/
            isVideoAllFunctuonReady: false,
            /*视频信息*/
            videoContentOptions: {
                /*视频名称*/
                name: "读取中",
                /*视频播放次数*/
                playTimes: 0,
                /*发布时间*/
                createTime: "读取中",
                /*视频简介*/
                videoIntro: "读取中",
                /*点赞数*/
                goodCount: '读取中',
                /*收藏数*/
                collection: '读取中',
                /*是否缓存*/
                download: false,
                /*是否已经点赞*/
                isGood: 0,
                /*是否已经收藏*/
                isCollected: 0,
                /*是否是vip视频*/
                isVip: 0,
                /*是否是G点视频 0是免费，大于0是需要的G点*/
                point: 0,
                /*是否已经购买*/
                isBuy: 0,
                /*视频标签*/
                tags: [],
                /*视频时长*/
                length: "",
                /*视频剧照*/
                posterImgList:[]
            },
            /*当前展开选项*/
            collapseOptions: {
                active: []
            },
            /*正式视频video配置*/
            playerOptions: {
                /**/
                id: "",
                /*视频播放地址*/
                source: "",
                /*封面*/
                cover: "",
                /*视频窗口宽度*/
                width: "100%",
                /*视频窗口高度*/
                /*控制面板的实现*/
                controlBarVisibility: 'always',
                /*声明视频播在界面上的位置，默认为“center”。可选值为：“top”，“center”*/
                x5_video_position: 'top',
                /*声明TBS播放器支持的方向，可选值：landscape：横屏  portraint：竖屏*/
                x5_orientation: 'portraint',
                /*声明TBS全屏播放是否横屏，默认值为true。*/
                x5LandscapeAsFullScreen: true,
                /*是否自动播放*/
                autoplay: true,
                /*启用hls插件播放*/
                useHlsPluginForSafari: true
            },
            /*正式视频对象*/
            formatVideoObj: null,
            /*正式视频占位对象*/
            formatVideoCopyObj: null,
            /*试看视频对象*/
            tryVideoObj: null,
            /*正式视频地址*/
            formatUrl: null,
            /*试看视频地址*/
            tryVideoUrl: null
        }
    },
    created() {
        /*回到步骤0*/
        this.resetVideoStatus();
        this.getAdFn();
        /*获取正式地址*/
        this.getFirstData();
    },
    mounted() {

    },
    beforeRouteLeave(to, from, next) {
        console.log(to);
        /*回到步骤0*/
        this.resetVideoStatus();
        next();
    },
    watch: {
        '$route'(val) {
            /*回到步骤0*/
            this.resetVideoStatus();
            /*获取正式地址*/
            this.getFirstData();
        }
    }
}
</script>

