<template>
    <div class="publicAllContentBg" id="scrollVideoList">
        <!--标题-->
        <div class="publicContentHeaderBox">
            <div class="left"><van-icon :name="require('../assets/icon_free_nor.png')"></van-icon>限时免费</div>
            <div class="right" @click="$emit('goMore')">
                查看更多
            </div>
        </div>
        <!--视频列表-->
        <van-swipe :loop="false" :width="180" :show-indicators="false">
            <van-swipe-item v-for="(item,index) in videoList" :key="index">
                <div class="videoListStyle">
                    <!--图片区域(非广告)-->
                    <div class="imgArea" @click="goToVideoContent(item)">
                        <aesImage :file-url="item.coverImgUrl" :primordial-img="false"></aesImage>
                        <!--热度和时长-->
                        <div class="bottomInfo">
                            <div class="left">
                                <!--<i class="el-icon-thumb"></i>
                                {{$globalFn.returnHotTextFn(item.hot)}}-->
                            </div>
                            <div class="time" v-html="getVideoTime(item.length)"></div>
                        </div>
                    </div>
                    <!--底部文字(非广告)-->
                    <p class="title"  @click="goToVideoContent(item)">{{ item.name }}</p>
                    <!--底部视频标签(尚未hover)-->
                    <ul class="bottomSignBox">
                        <li>
                            <p class="one">
                                <!--尚未hover-->
                                <van-icon :name="require('../assets/icon_browse_nor.png')"/>
                                {{ $globalFn.returnHotTextFn(item.seeCount)}}
                            </p>
                        </li>
                        <li v-if="item.point===0&&item.isVip===0&&item.isFeatured===0">
                            <p class="two">
                                <!--尚未hover-->
                                <van-icon :name="require('../assets/icon_vip_nor.png')"/>
                                免费
                            </p>
                        </li>
                        <li v-if="item.isFeatured===1">
                            <p class="three">
                                <!--尚未hover-->
                                <van-icon :name="require('../assets/icon_official_nor.png')"/>
                                官方严选
                            </p>
                        </li>
                    </ul>
                    <!--底部视频标签（hover)-->
                    <!--<ul class="bottomSignBox hover">
                        <li>
                            <p class="one hover">
                                &lt;!&ndash;hover&ndash;&gt;
                                <van-icon :name="require('../assets/icon_browse_sel.png')"/>
                                {{ $globalFn.returnHotTextFn(item.seeCount)}}
                            </p>
                        </li>
                        <li v-if="item.point===0&&item.isVip===0&&item.isFeatured===0">
                            <p class="two hover">
                                &lt;!&ndash;hover&ndash;&gt;
                                <van-icon :name="require('../assets/icon_vip_sel.png')"/>
                                免费
                            </p>
                        </li>
                        <li v-if="item.isFeatured===1">
                            <p class="three hover">
                                &lt;!&ndash;hover&ndash;&gt;
                                <van-icon :name="require('../assets/icon_official_sel.png')"/>
                                官方严选
                            </p>
                        </li>
                    </ul>-->
                </div>
            </van-swipe-item>
        </van-swipe>
    </div>
</template>

<script>
import aesImage from "./aesImage";
export default {
    name: "publicScrollVideoList",
    components:{
        aesImage
    },
    props:{
        videoList:Array
    },
    data(){
        return {
            /*滚动列表配置*/
            bannerConfig:{
                slidesPerView: 1.3,
                spaceBetween: 30,
                centeredSlides: true,
                // loopAdditionalSlides:100,
               /* autoplay:{
                    delay:3000,
                    disableOnInteraction:false
                },*/
            }

        }
    },
    methods:{
        /*去视频详情*/
        goToVideoContent(item) {
            this.$router.push({name:'videoContent',params:{id:item.id}});
        },
        /*获取视频时长*/
        getVideoTime(time) {
            return this.$globalFn.getVideoTimeLength(parseInt(time));
            /*return this.$globalFn.getVideoTimeLength(3600);*/
        }
    },
    created() {

    }
}
</script>

<style scoped>
.bottomSignBox > li p.one.hover{
    border-color: #F54B64;
    color:#F54B64;
}
.bottomSignBox > li p.two.hover{
    border-color: #1DA362;
    color:#1DA362;
}
.bottomSignBox > li p.three.hover{
    border-color: #F7C3A7;
    color:#F7C3A7;
}
.bottomSignBox {
    display: flex;
    margin-top: 4px;
}

.bottomSignBox > li {
    margin-left: 6px;
}

.bottomSignBox > li:first-child {
    margin-left: 0;
}
.bottomSignBox > li p i.hover {
    display: none;
}
.bottomSignBox > li p i {
    margin-right: 4px;
}

.bottomSignBox > li p {
    color: #5E626C;
    /*border: 1px solid #5E626C;*/
    border-radius: 4px;
    font-size: 11px;
    height: 20px;
    display: flex;
    /*padding: 0 4px;*/
    box-sizing: border-box;
    align-items: center;
}

/*.videoListStyle:hover .title{
    color: #EB4A76;
}*/
.videoListStyle > .title {
    color: #EAF1FF;
    font-size: 14px;
    height: 40px;
    line-height: 20px;
    overflow: hidden;
    margin-bottom: 4px;
}

.videoListStyle > .imgArea > .bottomInfo {
    width: 100%;
    position: absolute;
    bottom:0;
    left: 0;
    display: flex;
    justify-content: space-between;
    color: #FFFFFF;
    padding: 0 4px 1px 4px;
    font-size: 12px;
    box-sizing: border-box;
    background: linear-gradient(0deg, rgba(57, 57, 56, 0.8) 1%, rgba(104, 104, 104, 0) 100%);
}
.videoListStyle:hover .popup{
    display: flex;
}
.videoListStyle .popup {
    width: 100%;
    height: 162px;
    z-index: 9999;
    position: absolute;
    align-items: center;
    justify-content: center;
    left: 0;
    top: 0;
    background-color: rgba(0, 0, 0, 0.5);
    display: none;
}

.videoListStyle > .imgArea > .triangle span {
    position: absolute;
    left: 2px;
    top: 5px;
    z-index: 999;
    font-size: 10px;
    transform: rotate(-32deg);
    color: #fff;
}

.videoListStyle > .imgArea > .triangle {
    position: relative;
}

.videoListStyle > .imgArea > .triangleRed:after {
    border-top: 34px solid #CB266D;
    left: 0;
    content: "";
    border-right: 49px solid transparent;
    position: absolute;
    top: 0;
    z-index: 998;
}

.videoListStyle > .imgArea > .triangleGreen:after {
    border-top: 34px solid #12CC82;
    left: 0;
    content: "";
    border-right: 49px solid transparent;
    position: absolute;
    top: 0;
    z-index: 998;
}

.videoListStyle > .imgArea {
    width: 100%;
    position: relative;
    overflow: hidden;
    box-sizing: border-box;
}

.videoListStyle {
    width: 100%;
    height: auto;
    overflow: hidden;
    box-sizing: border-box;
    padding: 5px 0;
    /*border-radius: 4px;*/
    cursor: pointer;
}
.videoListStyle:first-child,.videoListStyle:nth-child(2){
    padding-top: 0;
}
/*顶部标题*/
.publicContentHeaderBox{
    width: 100%;
    height: auto;
    overflow: hidden;
    box-sizing: border-box;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 0;
}
.publicContentHeaderBox>div.left{
    color: #EAF1FF;
    font-size: 18px;
    display: flex;
    align-items: center;
}
.publicContentHeaderBox>div.left i{
    margin-right: 10px;
}
.publicContentHeaderBox>div.right{
    color: #A0A4AE;
    display: flex;
    align-items: center;
    font-size: 14px;
}
</style>



