<template>
    <div class="publicAllContentBg allIndex" id="discoverIndex" ref="discoverIndex">
        <publicTopHeader></publicTopHeader>
        <div class="publicNeedXPaddingBg marginTop">
<!--            <van-pull-refresh v-model="refreshLoadingStatus" @refresh="onRefresh">-->
                <van-empty
                    v-if="loadingListOptions.list.length===0&&loadingListOptions.firstLoadingDone"
                    description="暂无视频"/>
                <!--视频列表-->
                <van-list
                    v-if="loadingListOptions.list.length>0&&loadingListOptions.firstLoadingDone"
                    v-model="loadingListOptions.loading"
                    :finished="loadingListOptions.finished"
                    @load="onLoad"
                    :immediate-check="false"
                    :offset="200"
                >
                    <publicVideoListModule :videoList="loadingListOptions.list"></publicVideoListModule>
                </van-list>
<!--            </van-pull-refresh>-->
        </div>

    </div>

</template>

<script>
import publicVideoListModule from "../../components/publicVideoListModule";
import publicTopHeader from "../../components/publicTopHeader";

export default {
    name: "discoverIndex",
    components: {
        publicVideoListModule,
        publicTopHeader
    },
    data() {
        return {
            /*下拉刷新是否在loading*/
            refreshLoadingStatus: false,
            /*列表配置*/
            loadingListOptions: {
                /*第一次加载*/
                firstLoadingDone: false,
                /*是否在加载*/
                loading: false,
                /*是否已经结束*/
                finished: false,
                /*列表*/
                list: [],
                /*当前页数*/
                current: 1,
                /*总页数*/
                totalPage: 0,
                /*总条数*/
                totalCount: 0,
                /*每页条数*/
                pageSize: 20
            },
            /*距离顶部位置*/
            scroll:0
        }
    },
    methods: {
        /*继续加载*/
        onLoad() {
            console.log("触底加载");
            /*请求视频列表*/
            this.getList(1);
            // console.log(this.loadingListOptions);
        },
        /*下拉刷新*/
        onRefresh() {
            /*清空目前数组*/
            this.loadingListOptions.list = [];
            /*开启动画*/
            /*this.$toast.loading({
                message: '加载中...',
                forbidClick: true,
                duration:0
            });*/
            /*开启loading*/
            this.loadingListOptions.firstLoadingDone = false;
            this.getList(0);
        },
        /*获取列表*/
        getList(type) {
            this.$requestApi.getFindVideo({
                length: this.loadingListOptions.pageSize,
                page: type === 1 ? this.loadingListOptions.current + 1 : 1,
            })
                .then(res => {
                    /*如果当前广告字段存在且当前是首次请求，那么就放置广告*/
                    if (type === 0 && res.data.list.length > 0) {
                        for (let obj of this.$store.state.adList) {
                            if (obj.name === "h5titlepage") {
                                res.data.list.splice(1, 1, {
                                    coverImgUrl: obj.imgUrl,
                                    isAd: true,
                                    name: obj.title
                                });
                                break;
                            }
                        }
                    }
                    /*放置视频数据*/
                    type === 0 ? this.loadingListOptions.list = res.data.list : this.loadingListOptions.list = this.loadingListOptions.list.concat(res.data.list);
                    /*放置总页数*/
                    this.loadingListOptions.totalPage = this.$globalFn.totalPageCountFn(this.loadingListOptions.pageSize, res.data.recordsTotal);
                    /*放置当前页数*/
                    this.loadingListOptions.current = res.data.page;
                    /*记录总条数*/
                    this.loadingListOptions.totalCount = res.data.recordsTotal;
                    /*console.log("打印当前页数");
                    console.log(this.loadingListOptions.current);
                    console.log("打印当前总页数");
                    console.log(this.loadingListOptions.totalPage);*/
                    /*判断是否已经到最后一页，如果是，那么就停止下拉加载*/
                    if (this.loadingListOptions.totalPage === this.loadingListOptions.current) {
                        this.loadingListOptions.finished = true;
                    } else if (this.loadingListOptions.totalPage === 1) {
                        this.loadingListOptions.finished = true;
                    }
                    /*关闭加载动画*/
                    this.loadingListOptions.loading = false;
                    /*关闭下拉加载*/
                    this.refreshLoadingStatus = false;
                    /*关闭第一次loading*/
                    this.loadingListOptions.firstLoadingDone = true;
                    /*关闭加载动画*/
                    this.$toast.clear();
                })
                .catch(err => {

                })
        },
        /*获取位置*/
        publicReturnScrollPos(){
            console.log('打印位置');
            console.log(this.$refs.discoverIndex.scrollTop);
            return this.$refs.discoverIndex.scrollTop;
        }
    },
    created() {
        this.getList(0);
    },
    mounted() {

    },
    beforeRouteEnter(to, from, next) {
        /*如果是来自，那么就不需要启用旧数据，重新读取*/
        /*if (from.name === "menu") {
            to.meta.useOldData = false;
        }
        else if (from.name === "goodsContent") {
            //如果是来自商品详情,那么就启用旧数据
            to.meta.useOldData = true;
        }*/
        next();
    },

    beforeRouteLeave(to, from, next){
        console.log('打印取的位置');
        console.log(to);
        /*如果是去详情，那么记住滚动位置*/
        if(to.name==="linkPage"){
            this.$store.commit('changeScrollPos',this.publicReturnScrollPos());
            /*console.log('打印离开钱位置');
            console.log(this.$globalFn.publicReturnScrollPos())*/
            next();
        }
        next();
    },
    /*激活(页面被缓存的情况下)*/
    activated() {
        console.log('激活');
        this.$refs.discoverIndex.scrollTop = this.$store.state.scrollPosition;
    }
}
</script>

<style scoped>

</style>



