<template>
    <div :id="id"></div>
</template>

<script>
import 'xgplayer';
import HlsJsPlayer from "xgplayer-hls.js";

export default {
    name: "xgPlayer",
    props:{
        id:String,
        url:String
    },
    data(){
        return {
            /*保存变量*/
            videoObj:null,
            /*存储id*/
            videoDomId:null,
            /*视频url*/
            videoUrl:null
        }
    },
    methods:{
    },
    created() {
        this.videoDomId=this.id;
        this.videoUrl=this.url;
    },
    mounted() {
        console.log(this.videoUrl);
        this.videoObj = new HlsJsPlayer({
            id: this.videoDomId,
            url: this.videoUrl,
            fluid: true,
            videoInit: true,
            /*rotate: {   //视频旋转按钮配置项
                innerRotate: true, //只旋转内部video
                clockwise: false // 旋转方向是否为顺时针
            },*/
            /*width:500,*/
            /*视频适应方式
            * 1、fitVideoSize设置为fixWidth，表示容器宽度固定，容器高度按照视频内容比例增大或减小；
            * 2、fitVideoSize设置为fixHeight，表示容器高度固定，容器宽度按照视频内容比例增大或减小；
            * 3、fitVideoSize设置为auto，表示容器高度或宽度按照视频内容比例增大。
            * */
            fitVideoSize: 'fixHeight',
            /*自动播放*/
            autoplay:false,
            /*音量(0~1)*/
            volume: 0.5,
            /*倍速*/
            /*playbackRate: [0.5, 0.75, 1, 1.5, 2],*/
            playsinline: true,
            useHls: true,
            closeVideoTouch:true
        });
        /*注册事件*/
        this.videoObj.once('ready', () => {
            this.videoObj.src  = this.videoUrl;
            /*console.log(this.videoObj.src);
            console.log('实例化完成');*/
            /*注册开始播放事件*/
            this.videoObj.on('play', ()=>{
                this.$emit('videoOnClick')
            });
            /*注册暂停播放事件*/
            this.videoObj.on('pause', ()=>{
                this.$emit('videoOnPause')
            });
            if(this.videoDomId==="formatVideo"){
                /*如果是正式播放器，注册如下结束事件*/
                this.videoObj.on('ended', ()=>{
                    this.$emit('officialVideoPlayDone');
                });
            }
            else if(this.videoDomId==="tryVideo"){
                /*注册播放结束事件*/
                this.videoObj.on('ended', ()=>{
                    this.$emit('tryWatchVideoEnded');
                });
            }
            /*如果不是占位video*/
            if(this.videoDomId!=='formatVideoCopy'){
                /*直接开始播放*/
                this.videoObj.play();
            }
        });
    },
    destroyed() {
        /*销毁播放器*/
        this.videoObj.destroy(true);
    }
}
</script>

<style scoped>

</style>



