<template>
    <div class="publicAllContentBg fullHeight" id="indexMenuVideoModule">
        <!--loading动画-->
        <publicLoadingAnm v-if="!loadingListOptions.firstLoadingDone"></publicLoadingAnm>
        <!--视频列表-->
        <publicVideoListModule
            v-if="loadingListOptions.list.length>0&&loadingListOptions.firstLoadingDone"
            :videoList="loadingListOptions.list"
        ></publicVideoListModule>
        <!--分页-->
        <van-pagination
            v-if="loadingListOptions.firstLoadingDone&&loadingListOptions.list.length>0"
            v-model="loadingListOptions.current"
            style="margin-top: 10px"
            :total-items="loadingListOptions.totalCount"
            @change="pageChange"
            :page-count="loadingListOptions.totalPage"
            :items-per-page="loadingListOptions.pageSize"
            mode="simple"/>
        <!--暂无视频-->
        <noResult v-if="loadingListOptions.firstLoadingDone&&loadingListOptions.list.length===0" desc="暂无视频"></noResult>
    </div>
</template>

<script>
import publicVideoListModule from "../../components/publicVideoListModule";
import publicLoadingAnm from "../../components/publicLoadingAnm";
import noResult from "../../components/noResult";
export default {
    name: "indexMenuVideoModule",
    props: {
        menuId: Number
    },
    components: {
        publicVideoListModule,
        publicLoadingAnm,
        noResult
    },
    data() {
        return {
            /*列表配置*/
            loadingListOptions: {
                /*第一次加载*/
                firstLoadingDone: false,
                /*是否在加载*/
                loading: false,
                /*是否已经结束*/
                finished: false,
                /*列表*/
                list: [],
                /*当前页数*/
                current: 1,
                /*总页数*/
                totalPage: 0,
                /*总条数*/
                totalCount: 0,
                /*每页条数*/
                pageSize: 12
            }
        }
    },
    methods: {
        /*清空当前页面*/
        resetData(){
            this.loadingListOptions.totalPage=0;
            this.loadingListOptions.totalCount=0;
            this.loadingListOptions.totalCount=1;
            this.loadingListOptions.list=[];
            this.loadingListOptions.firstLoadingDone=false;
            this.loadingListOptions.finished=false;
        },
        pageChange(e){
            console.log(e);
            this.loadingListOptions.list=[];
            /*开启loading*/
            this.loadingListOptions.firstLoadingDone=false;
            this.getVideoListFn(e);
        },
        /*获取视频列表的方法*/
        getVideoListFn(page) {
            /*
            * type：是否是继续加载，1是0不是
            * */
            console.log("获取视频列表");
            /*如果是重新加载，那么清空数据*/
            if (page === 1) {
                /*清空数据*/
                this.loadingListOptions.list = [];
                /*清空总页数*/
                this.loadingListOptions.totalPage = 0;
                /*清空当前页数*/
                this.loadingListOptions.current = 1;
                /*允许继续下拉*/
                this.loadingListOptions.finished = false;
                this.loadingListOptions.firstLoadingDone = false;
            }
            /*请求*/
            this.$requestApi.getVideoList({
                length: this.loadingListOptions.pageSize,
                page:page,
                typeId:this.$route.params.id,
            })
                .then(res => {
                    /*放置视频数据*/
                    this.loadingListOptions.list = res.data.list;
                    /*放置总页数*/
                    this.loadingListOptions.totalPage = this.$globalFn.totalPageCountFn(this.loadingListOptions.pageSize, res.data.recordsTotal);
                    /*放置当前页数*/
                    this.loadingListOptions.current = res.data.page;
                    /*记录总条数*/
                    this.loadingListOptions.totalCount = res.data.recordsTotal;
                    /*关闭加载动画*/
                    this.loadingListOptions.loading = false;
                    this.loadingListOptions.firstLoadingDone = true;
                })
                .catch(err => {
                    console.log('视频类型不存在');
                })
        },
    },
    created() {
        /*清空当前*/
        this.resetData();
        this.getVideoListFn(1);
    },
    watch:{
        '$route'(val,old){
            /*console.log('重置');
            console.log(val);
            console.log(old);*/
            /*如果去的页面是menu，且上一页不是视频详情，那么才重新获取数据*/
            if(val.name==="menuPage"&&old.name!=="videoContent"){
                /*清空当前*/
                this.resetData();
                this.getVideoListFn(1);
            }

        }
    }
}
</script>

<style scoped>

</style>



