<template>
    <div class="publicAllContentBg allIndex fullHeight" id="index">

        <!--顶部header-->
        <publicTopHeader></publicTopHeader>
        <!--<van-pull-refresh v-model="refreshLoadingStatus" @refresh="onRefresh">-->
            <!--顶部tab切换-->
            <div class="indexTopTabBox">
                <van-tabs v-model="indexTopTabConfig.nowSelect"
                          id="indexTabs"
                          @change="titleOnChange"
                          color="#F54B64"
                >
                    <van-tab title="推荐" :name="0"></van-tab>
                    <van-tab :title="item.typeName" :name="item.id" v-for="(item,index) in indexTopTabConfig.list" :key="index"></van-tab>
                </van-tabs>
                <keep-alive>
                    <router-view/>
                </keep-alive>
            </div>
        <!--</van-pull-refresh>-->
    </div>

</template>

<script>

import publicTopHeader from "../components/publicTopHeader";

import indexMenuVideoModule from "../components/indexMenuVideoModule";

export default {
    name: "index",
    components: {
        publicTopHeader,
        indexMenuVideoModule
    },
    data() {
        return {
            /*是否在刷新*/
            refreshLoadingStatus:false,
            /*首页顶部tab配置*/
            indexTopTabConfig: {
                /*渲染列表(只有一级分类)*/
                list: [],
                /*目前选中*/
                nowSelect: 0
            }

        }
    },
    methods: {
        /*重置选项*/
        resetActive(){
            if(this.$route.name==="indexInfo"){
                this.indexTopTabConfig.nowSelect=0;
                return;
            }
            /*console.log('打印当前id');
            console.log(Number(this.$route.params.id));*/
            if(this.indexTopTabConfig.nowSelect!==Number(this.$route.params.id)){
                this.indexTopTabConfig.nowSelect=Number(this.$route.params.id);
            }
        },
        titleOnChange(name,title){
            console.log(name);
            console.log(title);
            switch (name){
                case 0:
                    this.$router.replace({name:'indexInfo'});
                    break;
                default:
                    this.$router.replace({name:'menuPage',params:{id:name}});
                    break;
            }
        },
        goFreeVideoList(){

        },
        /*下拉刷新*/
        onRefresh() {
            /*清空目前数组*/
            this.indexTopTabConfig.list = [];
            this.indexTopTabConfig.nowSelect = 0;
            this.indexBannerConfig.list=[];
            for(let obj of this.indexVideoModule){
                obj.videoList=[];
            }
            this.getMenu();
            this.getBanner();
            this.getFreeVideoFn(1);
            this.getRankVideoFn(1);
            this.getHotRankVideoFn(1);
            this.refreshLoadingStatus=false;
        },

        /*请求一级分类*/
        getMenu() {
            this.$requestApi.getCategoryList({})
                .then(res => {
                    console.log(res);
                    this.indexTopTabConfig.list = res.data.list.map(el => {
                        el.videoList = [];
                        return el;
                    });
                    /*循环请求当前分类下*/
                    this.resetActive();
                })
                .catch(res => {
                    console.log("失败");
                    console.log(res);
                });
        }
    },
    created() {
        this.getMenu();
    },
    activated() {
        this.resetActive();
    }
}
</script>

<style scoped>
.indexTopTabBox {
    width: 100%;
    box-sizing: border-box;
    padding: 0 10px;
}
</style>



