<template>
    <div class="publicAllContentBg fullHeight">
        <!--顶部tabbar-->
        <van-nav-bar
            title="常见问题"
            class="publicAllNavbarClass"
            :border="false"
            left-arrow
            @click-left="$router.go(-1)"
        />
        <div class="publicNeedXPaddingBg">
            <!--底部按钮-->
            <ul class="questionList">
                <li v-for="(item,index) in questionList" :key="index">
                    <div @click="goContent(index)">
                        <p>{{item.name}}</p>
                    </div>
                </li>
            </ul>
        </div>

    </div>
</template>

<script>
import questionList from '../../../../static/lib/questions.json'
export default {
    name: "questionIndex",
    data(){
        return {
            questionList:questionList
        }
    },
    methods:{
        goContent(index){
            this.$router.push({name:'questionContent',params:{index:index}});
        }
    },
    created() {

    }
}
</script>

<style scoped>
    .questionList{
        width: 100%;
        overflow: hidden;
        box-sizing: border-box;
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
    }
    .questionList>li{
        width: 50%;
        box-sizing: border-box;
        padding: 5px 5px;
    }
    .questionList>li>div{
        width: 100%;
        background-color: #303542;
        height: 82px;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .questionList>li>div p{
        font-size: 16px;
        color: #fafafa;
    }
</style>



