<template>
    <div class="publicAllContentBg" id="publicTopHeader">
        <!--顶部导航-->
        <div class="topHeaderBg">
            <!--左侧logo-->
            <div class="left">
                <img :src="require('../assets/img_logo.png')" alt="">
            </div>
            <!--右侧快捷访问-->
            <!--<div class="right">
                <p>网站公告</p>
                <span class="publicTopHeaderLine"></span>
                <p>加盟赚钱</p>
                <span class="publicTopHeaderLine"></span>
                <p>代理后台</p>
            </div>-->
        </div>
        <!--搜索栏-->
        <div class="topInputBox">
            <van-field
                left-icon="search"
                readonly
                @click="goToSearchPage"
                :placeholder="$globalFn.returnPublicPlaceholder()"
            ></van-field>
        </div>
    </div>

</template>

<script>
export default {
    name: "publicTopHeader",
    data(){
        return {

        }
    },
    methods:{
        /*跳转搜索页面*/
        goToSearchPage() {
            this.$router.push({name:'searchPage'});
        }
    }
}
</script>

<style scoped>
    .topHeaderBg{
        width: 100%;
        box-sizing: border-box;
        padding: 10px 10px;
        display: flex;
        justify-content: space-between;
    }
    .topHeaderBg>.left{
        width: 34%;
    }
    .topHeaderBg>.left img{
        display: block;
        width: 100%;
    }
    .topHeaderBg>.right{
        width: 64%;
        display: flex;
        align-items: center;
        justify-content: flex-end;
    }
    .topHeaderBg>.right p{
        color: #A0A4AE;
        font-size: 12px;
    }
    .publicTopHeaderLine{
        width: 1px;
        background-color: #686c75;
        height: 12px;
        margin: 0 8px;
        display: inline-block;
    }
    .topInputBox{
        width: 100%;
        box-sizing: border-box;
        padding: 0 10px;
    }
</style>



