/*请求封装(基于axios)*/
import globalTools from "../global/globalTools";
import {Toast} from "vant";
import axios from "axios";
import store from "../store";
import vm from "../main";
import router from '../router/index';
const crypto = require('crypto');

/*寻找有没有登录不登录都可以的url*/
const urlList=['/video/getInfo','/actor/getActorInfo','/video/getUrl','/video/findVideo'];


function aesEncrypt(data, key) {
    const cipher = crypto.createCipher('aes192', key);
    var crypted = cipher.update(data, 'utf8', 'hex');
    crypted += cipher.final('hex');
    return crypted;
}

function aesDecrypt(encrypted, key) {
    const decipher = crypto.createDecipher('aes192', key);
    var decrypted = decipher.update(encrypted, 'hex', 'utf8');
    decrypted += decipher.final('utf8');
    return decrypted;
}


export const requestFn=(obj)=>{
   /* console.log("打印请求参数");
    console.log(obj);*/
    /*默认如果未定义都是需要登录的请求*/
    return new Promise((resolve, reject)=>{
        /*是否是需要登录的请求*/
        if(obj.needLogin===true&&globalTools.globalFunctions.userIsLogin()===false){
            /*过滤传不传递token都可以的请求*/
            if(urlList.indexOf(obj.url)===-1){
                /*提示登录*/
                Toast.fail('请先登录!');
                router.replace({name:"login"});
                return;
            }

        }
        axios({
            url:globalTools.globalFunctions.returnNowApiBaseUrl()+obj.url,
            method:obj.method===undefined?'GET':obj.method,
            params:obj.params===undefined?{}:obj.params,
            data:{
                clientType:globalTools.globalFunctions.whichClientIs(),
                ...obj.data
            },
            headers:{
                /*unix时间戳*/
                timestamp:Math.round(new Date().getTime()/1000),
                /*uuid*/
                timestr:globalTools.globalFunctions.getSignCodeStr(),
                /*token*/
                Token:obj.needLogin===true&&store.state.isLogin===true?store.state.userInfo.token:""
            },
            dataType:obj.dataType===undefined?"JSON":obj.dataType,
            timeout: 6000
        })
            .then((res)=>{
                /*如果请求尚未成功统一先弹出错误*/
                if(res.data.code!==1&&obj.url!=='/video/getUrl'){
                    /*console.log('并不等于1');
                    console.log(res);*/
                    Toast.fail({
                        message:res.data.message,
                        duration:1000
                    });
                }
                var key = 'Password!';
                var serializer_data = aesDecrypt(res.data.data, key);
                res.data.data = JSON.parse(serializer_data);
                /*状态码处理*/
                switch (res.data.code) {
                    case 1:
                        /*请求成功*/
                        resolve(res.data);
                        break;
                    case 2:
                        /*请求失败*/
                        /*Toast('网络错误，请稍后再试');*/
                        reject(res.data);
                        break;
                    case 3:
                        /*token验证失败，请重新登录*/
                        /*直接跳转登录页面*/
                        /*Toast('请重新登录！');*/
                        /*删除本地用户信息*/
                        store.commit('changeLoginStatus', false);
                        store.commit('userInfoSave', null);
                        /*清除本地缓存*/
                        localStorage.removeItem('videoMobileDarkStore');
                        router.push({name:"login"});
                        reject(res.data);
                        break;
                    case 4:
                        /*token过期*/
                        /*直接跳转登录页面*/
                        /*删除本地用户信息*/
                        store.commit('changeLoginStatus', false);
                        store.commit('userInfoSave', null);
                        /*清除本地缓存*/
                        localStorage.removeItem('videoMobileDarkStore');
                        router.push({name:"login"});
                        reject(res.data);
                        break;
                    case 11:
                        /*登录挤掉*/
                        /*Toast('请重新登录！');*/
                        /*删除本地用户信息*/
                        store.commit('changeLoginStatus', false);
                        store.commit('userInfoSave', null);
                        /*清除本地缓存*/
                        localStorage.removeItem('videoMobileDarkStore');
                        /*直接跳转登录页面*/
                        router.push({name:"login"});
                        reject(res.data);
                        break;
                    default:
                        reject(res.data);
                }
            })
            .catch((err)=>{
                console.log(err);
                /*Toast.fail({
                    message:err.data.message,
                    duration:1000
                });*/
            });
    });
}



