<template>
    <div class="publicIndexVideoMoudleBox">
        <!--标题-->
        <div class="publicContentHeaderBox">
            <div class="left"><van-icon :name="require('../assets/icon_guess_nor.png')"></van-icon>猜你喜欢</div>
        </div>
        <!--缺省-->
        <noResult desc="暂无视频" v-if="loadingListOptions.list.length===0"></noResult>
        <!--猜你喜欢-->
        <publicVideoList v-if="loadingListOptions.list.length>0" :video-list="loadingListOptions.list"></publicVideoList>

    </div>
</template>

<script>
    import publicVideoList from "./publicVideoListModule";
    import aesImage from "./aesImage";
    import publicModuleHeader from "./publicModuleHeader";
    import noResult from "./noResult";
    export default {
        name: "guessYourLike",
        props: {
        },
        components:{
            publicModuleHeader,
            aesImage,
            publicVideoList,
            noResult
        },
        data() {
            return {
                /*加载更多配置*/
                loadingListOptions:{
                    /*是否在加载*/
                    loading:false,
                    /*是否已经结束*/
                    finished:false,
                    /*每页个数*/
                    pageSize:10,
                    /*列表*/
                    list:[
                        /*{
                            id: 1,
                            name: "第六届科幻联想儿童创意大赛官方规则说明",
                            img: require('../assets/indexBanner.jpg'),
                            type: 1,
                            hot: 1000,
                            time: "10:56"
                        },
                        {
                            id: 2,
                            name: "第六届科幻联想儿童创意大赛官方规则说明",
                            img: require('../assets/indexBanner.jpg'),
                            type: 2,
                            hot: 1230,
                            time: "10:56"
                        }*/
                    ],
                    /*当前页数*/
                    current:1,
                    /*总页数*/
                    totalPage:0,
                    /*总条数*/
                    totalCount:0
                }
            }
        },
        methods: {
            /*请求列表的方法*/
            getList(type){
                this.$requestApi.getGuassYourLike({
                    videoId:this.$route.params.id,
                    length:this.loadingListOptions.pageSize,
                    page:type===0?1:this.loadingListOptions.current+1
                })
                    .then(res=>{
                        /*放置视频数据*/
                        if(type===0){
                            this.loadingListOptions.list=res.data.list;
                        }
                        else{
                            this.loadingListOptions.list=this.loadingListOptions.list.concat(res.data.list);
                        }
                        /*放置总页数*/
                        this.loadingListOptions.totalPage=this.$globalFn.totalPageCountFn(this.loadingListOptions.pageSize,res.data.recordsTotal);
                        /*放置当前页数*/
                        this.loadingListOptions.current=res.data.page;
                        /*记录总条数*/
                        this.loadingListOptions.totalCount=res.data.recordsTotal;
                        /*console.log("打印当前页数");
                        console.log(this.loadingListOptions.current);
                        console.log("打印当前总页数");
                        console.log(this.loadingListOptions.totalPage);*/
                        /*判断是否已经到最后一页，如果是，那么就停止下拉加载*/
                        /*if(this.loadingListOptions.totalPage===this.loadingListOptions.current){
                            this.loadingListOptions.finished=true;
                        }
                        else if(this.loadingListOptions.totalPage===1){
                            this.loadingListOptions.finished=true;
                        }*/
                        this.loadingListOptions.finished=true;
                        this.loadingListOptions.loading=false;
                    })
                    .catch(res=>{
                        this.$toast.fail(res.message);
                    })
            },
            /*初始化请求方法*/
            getFirstData(){
                this.getList(0);
            },
            onLoad(){
                this.getList(1)
            },
            /*去视频详情*/
            goToVideoContent(item){
                this.$router.push({name:'linkPage',params:{id:item.id}});
            }
        },
        created() {
            console.log('获取当前视频id');
            console.log(this.$route.params.id);
            this.getFirstData();
        },
        mounted() {
        },
/*        watch:{
            $route(){
                /!*console.log('路由变化');
                window.location.reload();*!/

            }
        }*/
    }
</script>

<style scoped>
    /*具体video样式*/
    .videoMoudleLine {
        width: 100%;
        height: 2px;
        background-color: #F7F7F7;
    }

    .publicIndexVideoMoudleBox > .bottom > div.more {
        border-right: 2px solid #F7F7F7;
    }

    .publicIndexVideoMoudleBox > .bottom > div {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 50%;
        padding: 10px 0;
    }

    .publicIndexVideoMoudleBox > .bottom {
        width: 100%;
        margin-top: 2px;
        background-color: #FFFFFF;
        font-size: 14px;
        color: #979797;
        display: flex;
    }

    .videoListStyle > .title {
        color: #403f3f;
        font-size: 14px;
        height: 40px;
        line-height: 20px;
        overflow: hidden;
    }

    .videoListStyle > .imgArea > .bottomInfo {
        width: 100%;
        position: absolute;
        bottom: 0;
        left: 0;
        display: flex;
        justify-content: space-between;
        color: #FFFFFF;
        padding: 0 4px 1px 4px;
        font-size: 12px;
        box-sizing: border-box;
        background: linear-gradient(0deg,rgba(57,57,56,0.8) 1%,rgba(104,104,104,0) 100%);
    }

    .videoListStyle > .imgArea > .triangle span {
        position: absolute;
        left: 2px;
        top: 5px;
        z-index: 999;
        font-size: 10px;
        transform: rotate(-32deg);
        color: #fff;
    }

    .videoListStyle > .imgArea > .triangle {
        position: relative;
    }

    .videoListStyle > .imgArea > .triangleRed:after {
        border-top: 34px solid #CB266D;
        left: 0;
        content: "";
        border-right: 49px solid transparent;
        position: absolute;
        top: 0;
        z-index: 998;
    }

    .videoListStyle > .imgArea > .triangleGreen:after {
        border-top: 34px solid #12CC82;
        left: 0;
        content: "";
        border-right: 49px solid transparent;
        position: absolute;
        top: 0;
        z-index: 998;
    }

    .videoListStyle > .imgArea {
        width: 100%;
        position: relative;
        overflow: hidden;
        box-sizing: border-box;
        border-radius: 5px;
    }

    .videoListStyle {
        width: 50%;
        height: auto;
        overflow: hidden;
        box-sizing: border-box;
        padding: 5px;
        border-radius: 4px;
    }

    .videoListBox {
        width: 100%;
        height: auto;
        overflow: hidden;
        box-sizing: border-box;
        background-color: #FFFFFF;
        display: flex;
        align-content: flex-start;
        flex-flow: row wrap;
        padding: 0 5px 0 5px;

    }

    .publicIndexVideoMoudleBox {
        width: 100%;
        height: auto;
        overflow: hidden;
        box-sizing: border-box;
    }

    .publicIndexVideoMoudleBox > .top {
        padding: 10px 10px;
        box-sizing: border-box;
        width: 100%;
        height: auto;
        background-color: #FFFFFF;
    }

    .publicIndexVideoMoudleBox > .top > p {
        border-left: 4px solid #CB266D;
        padding-left: 10px;
        color: #404040;
        font-size: 18px;
        font-weight: 800;
    }
</style>



