<template>
    <div class="publicContetHeaderBox">
        <div class="left">{{moduleInfo.name}}</div>
        <div class="right" @click="goForMoreFn" v-if="moduleInfo.needMore">
            更多
            <van-icon name="arrow" />
        </div>
    </div>
</template>

<script>
    export default {
        name: "publicModuleHeader",
        props: {
            /*
            * {
            *    name:"", //模块名字
            *    url:""  //跳转的url
            *    needMore:Boolean //是否需要更多
            * }
            * */
            moduleInfo: Object
        },
        data() {
            return {

            }
        },
        methods:{
            /*去往更多*/
            goForMoreFn(){
                /*根据url进行跳转*/
                this.$router.push(this.moduleInfo.url)
            }
        }
    }
</script>

<style scoped>
    .publicContetHeaderBox{
        width: 100%;
        height: auto;
        overflow: hidden;
        box-sizing: border-box;
        display: flex;
        justify-content: space-between;
        align-items: baseline;
        padding: 10px;
    }
    .publicContetHeaderBox>div.left{
        border-left: 4px solid #CB266D;
        padding-left: 10px;
        color: #404040;
        font-size: 18px;
        font-weight: 800;
    }
    .publicContetHeaderBox>div.right{
        color: #979797;
        display: flex;
        align-items: center;
        font-size: 14px;
    }
</style>



