<template>
    <div class="publicAllContentBg" id="scrollSpecialList">
        <!--标题-->
        <div class="publicContentHeaderBox">
            <div class="left"><van-icon :name="require('../assets/icon_project_nor.png')"></van-icon>精选专题</div>
            <div class="right" @click="$router.push({name:'specialList'})">
                查看更多
            </div>
        </div>
        <!--专题列表-->
        <ul class="specialListBox">
            <li v-for="(item,index) in videoList" :key="index">
                <div class="videoListStyle">
                    <!--图片区域(非广告)-->
                    <div class="imgArea" @click="goToVideoContent(item)">
                        <aesImage :file-url="item.coverImageX" :primordial-img="false"></aesImage>
                    </div>
                    <!--底部文字(非广告)-->
                    <p class="title">{{ item.name }}</p>
                </div>
            </li>
        </ul>

        <!--<van-swipe :loop="false" :width="180" :show-indicators="false">
            <van-swipe-item v-for="(item,index) in videoList" :key="index">
                <div class="videoListStyle">
                    &lt;!&ndash;图片区域(非广告)&ndash;&gt;
                    <div class="imgArea" @click="goToVideoContent(item)">
                        <aesImage :file-url="item.coverImageY" :primordial-img="false"></aesImage>
                    </div>
                    &lt;!&ndash;底部文字(非广告)&ndash;&gt;
                    <p class="title">{{ item.name }}</p>
                </div>
            </van-swipe-item>
        </van-swipe>-->
    </div>
</template>

<script>
import aesImage from "./aesImage";
export default {
    name: "publicScrollVideoList",
    components:{
        aesImage
    },
    props:{
        videoList:Array
    },
    data(){
        return {
            /*滚动列表配置*/
            bannerConfig:{
                slidesPerView: 1.3,
                spaceBetween: 30,
                centeredSlides: true,
                // loopAdditionalSlides:100,
               /* autoplay:{
                    delay:3000,
                    disableOnInteraction:false
                },*/
            }

        }
    },
    methods:{
        /*去视频详情*/
        goToVideoContent(item) {
            this.$router.push({name:'specialContent',params:{id:item.id}});
        },
        /*获取视频时长*/
        getVideoTime(time) {
            return this.$globalFn.getVideoTimeLength(parseInt(time));
            /*return this.$globalFn.getVideoTimeLength(3600);*/
        }
    },
    created() {

    }
}
</script>

<style scoped>
.specialListBox{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}
.specialListBox>li{
    width: 49%;
}
.bottomSignBox > li p.one.hover{
    border-color: #F54B64;
    color:#F54B64;
}
.bottomSignBox > li p.two.hover{
    border-color: #1DA362;
    color:#1DA362;
}
.bottomSignBox > li p.three.hover{
    border-color: #F7C3A7;
    color:#F7C3A7;
}
.bottomSignBox {
    display: flex;
    margin-top: 4px;
}

.bottomSignBox > li {
    margin-left: 6px;
}

.bottomSignBox > li:first-child {
    margin-left: 0;
}
.bottomSignBox > li p i.hover {
    display: none;
}
.bottomSignBox > li p i {
    margin-right: 4px;
}

.bottomSignBox > li p {
    color: #5E626C;
    border: 1px solid #5E626C;
    border-radius: 4px;
    font-size: 11px;
    height: 20px;
    display: flex;
    padding: 0 4px;
    box-sizing: border-box;
    align-items: center;
}

/*.videoListStyle:hover .title{
    color: #EB4A76;
}*/
.videoListStyle > .title {
    color: #EAF1FF;
    font-size: 14px;
    height: 20px;
    line-height: 20px;
    overflow: hidden;
    margin-top: 5px;
    /*text-align: center;*/
}

.videoListStyle > .imgArea > .bottomInfo {
    width: 100%;
    position: absolute;
    bottom:0;
    left: 0;
    display: flex;
    justify-content: space-between;
    color: #FFFFFF;
    padding: 0 4px 1px 4px;
    font-size: 12px;
    box-sizing: border-box;
    background: linear-gradient(0deg, rgba(57, 57, 56, 0.8) 1%, rgba(104, 104, 104, 0) 100%);
}
.videoListStyle:hover .popup{
    display: flex;
}
.videoListStyle .popup {
    width: 100%;
    height: 162px;
    z-index: 9999;
    position: absolute;
    align-items: center;
    justify-content: center;
    left: 0;
    top: 0;
    background-color: rgba(0, 0, 0, 0.5);
    display: none;
}

.videoListStyle > .imgArea > .triangle span {
    position: absolute;
    left: 2px;
    top: 5px;
    z-index: 999;
    font-size: 10px;
    transform: rotate(-32deg);
    color: #fff;
}

.videoListStyle > .imgArea > .triangle {
    position: relative;
}

.videoListStyle > .imgArea > .triangleRed:after {
    border-top: 34px solid #CB266D;
    left: 0;
    content: "";
    border-right: 49px solid transparent;
    position: absolute;
    top: 0;
    z-index: 998;
}

.videoListStyle > .imgArea > .triangleGreen:after {
    border-top: 34px solid #12CC82;
    left: 0;
    content: "";
    border-right: 49px solid transparent;
    position: absolute;
    top: 0;
    z-index: 998;
}

.videoListStyle > .imgArea {
    width: 100%;
    position: relative;
    overflow: hidden;
    box-sizing: border-box;
}

.videoListStyle {
    width: 100%;
    height: auto;
    overflow: hidden;
    box-sizing: border-box;
    padding: 5px 0;
    /*border-radius: 4px;*/
    cursor: pointer;
}
.videoListStyle:first-child,.videoListStyle:nth-child(2){
    padding-top: 0;
}

</style>



