<template>
    <div class="publicAllContentBg" id="searchIndex">
        <!--顶部导航-->
        <publicTopNavbarWithCancel></publicTopNavbarWithCancel>
        <div class="publicNeedXPaddingBg">
            <!--热门搜索列表-->
            <ul class="hotSearchList">
                <li v-for="(item,index) in hotSearchOptions.list" :key="index">
                    <p @click="publicSearchWordFn(item.key)"><span>{{index+1}}</span>{{item.key}}</p>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
import publicTopNavbarWithCancel from "../../components/publicTopHeaderWithCancel";
export default {
    name: 'searchIndex',
    components:{
        publicTopNavbarWithCancel
    },
    data() {
        return {
            /*热门搜索配置*/
            hotSearchOptions:{
                /*列表*/
                list:[]
            },
            /*搜索栏配置*/
            inputOptions: {
                /*搜索值*/
                val: ""
            }
        }
    },
    methods: {
        /*去搜索结果页面*/
        goToResult(item){
            /*改变存储值*/
            this.$store.commit('changeSearchHistory',{
                type:1,
                val:item.key
            });
            /*存储信息*/
            this.$store.commit('changeSearchInfo',{
                type:1,
                info:item.key,
                videoType:1,
                sortFn:null
            });
            this.$router.push({name:"searchResult"});
        },
        /*关键字搜索方法*/
        publicSearchWordFn(word) {
            /*存储信息*/
            this.$store.commit('changeSearchInfo',{
                type:1,
                info:word,
                videoType:1,
                sortFn:null
            });
            this.$router.push({name:"searchResult"});
        },
        /*跳转分类页面*/
        clickInputRightIcon() {

        },
        /*初始化数据*/
        getFirstData(){
            this.$requestApi.getHotSearchList({
                length:8
            })
                .then(res=>{
                    console.log('打印热门');
                    console.log(res);
                    this.hotSearchOptions.list=res.data.list;
                })
                .catch(err=>{

                })
        },
        /*搜索热词*/
        hotSearchGet() {
            this.$requestApi.getHotSearchList({
                length: 8
            })
                .then(res => {
                    this.hotSearchOptions.list = res.data.list;
                })
                .catch(err => {

                })
        },
    },
    created() {
        this.getFirstData();
        this.hotSearchGet();
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
/*热门搜索*/
.hotSearchList{
    width:100%;
    height: auto;
    overflow: hidden;
    box-sizing: border-box;
}
.hotSearchList>li{
    margin-top: 14px;
}
.hotSearchList>li:first-child{
    margin-top: 0;
}
.hotSearchList>li p{
    color: #A0A4AE;
    font-size: 14px;
}

.hotSearchList>li p span{
    background-color: #616672;
    display: inline-block;
    padding: 6px 8px;
    color: #fafafa;
    font-size: 12px;
    margin-right: 6px;
    border-radius: 4px;
}
.hotSearchList>li:nth-child(1) p span,.hotSearchList>li:nth-child(2) p span,.hotSearchList>li:nth-child(3) p span{
    background-color: #F54B64;
}
</style>



