<template>
    <div class="publicAllContentBg fullHeight" id="specialList">
        <!--顶部tabbar-->
        <van-nav-bar
            title="专题列表"
            class="publicAllNavbarClass"
            :border="false"
            left-arrow
            left-text="返回"
            @click-left="$router.go(-1)"
        />
        <div class="publicNeedXPaddingBg">
            <publicLoadingAnm v-if="!loadingListOptions.firstLoadingDone"></publicLoadingAnm>
            <noResult desc="暂无专题"
                      v-if="loadingListOptions.list.length===0&&!loadingListOptions.firstLoadingDone"></noResult>
            <!--视频列表-->
            <van-list
                v-if="loadingListOptions.list.length>0&&loadingListOptions.firstLoadingDone"
                v-model="loadingListOptions.loading"
                :finished="loadingListOptions.finished"
                @load="onLoad"
                :immediate-check="false"
            >
                <!--精选专题广告位-->
                <ul class="specialListBox" v-if="loadingListOptions.list.length>0">
                    <li v-for="(item,index) in loadingListOptions.list" :key="index">
                        <div class="specialListStyle"
                             @click="$router.push({name:'specialContent',params:{id:item.id}})">
                            <div class="img">
                                <!--                            <img :src="require('../../assets/project_yuchuang@2x.png')" alt=""/>-->
                                <aesImage :primordial-img="false" :file-url="item.coverImageX"></aesImage>
                                <!--<van-image
                                    fit="cover"
                                    :src="item.coverImageY"
                                />-->
                            </div>
                            <!--<div class="bottom">
                                <p class="left">
                                    <van-icon name="fire-o" color="#FFFFFF"  style="margin-right: 4px" />
                                </p>
    &lt;!&ndash;                            <p class="right">16:23</p>&ndash;&gt;
                            </div>-->
                            <div class="text">
                                <p class="top">{{ item.name }}</p>
                                <p class="bottom">{{ item.description }}</p>
                            </div>
                        </div>
                    </li>
                </ul>
            </van-list>
        </div>

    </div>
</template>

<script>
import aesImage from "../../components/aesImage";
import noResult from "../../components/noResult";
import publicLoadingAnm from "../../components/publicLoadingAnm";

export default {
    name: 'specialList',
    components: {
        aesImage,
        publicLoadingAnm,
        noResult
    },
    methods: {
        /*获取列表*/
        getList(type) {
            this.$requestApi.getSpecialList({
                length: this.loadingListOptions.pageSize,
                page: type === 1 ? this.loadingListOptions.current + 1 : 1,
            })
                .then(res => {
                    if (res.data.list.length === 0) {
                        return;
                    }
                    if (type === 0) {
                        this.loadingListOptions.list = res.data.list;
                    } else {
                        this.loadingListOptions.list = this.loadingListOptions.list.concat(res.data.list);
                    }
                    /*放置总页数*/
                    this.loadingListOptions.totalPage = this.$globalFn.totalPageCountFn(this.loadingListOptions.pageSize, res.data.recordsTotal);
                    /*放置当前页数*/
                    this.loadingListOptions.current = res.data.page;
                    /*记录总条数*/
                    this.loadingListOptions.totalCount = res.data.recordsTotal;
                    console.log("打印当前页数");
                    console.log(this.loadingListOptions.current);
                    console.log("打印当前总页数");
                    console.log(this.loadingListOptions.totalPage);
                    /*判断是否已经到最后一页，如果是，那么就停止下拉加载*/
                    if (this.loadingListOptions.totalPage === this.loadingListOptions.current) {
                        this.loadingListOptions.finished = true;
                    } else if (this.loadingListOptions.totalPage === 1) {
                        this.loadingListOptions.finished = true;
                    }
                    /*关闭加载动画*/
                    this.loadingListOptions.loading = false;
                    this.loadingListOptions.firstLoadingDone = true;
                })
                .catch(err => {

                })
        },
        /*继续加载*/
        onLoad() {
            console.log("触底加载");
            this.getList(1);
        }
    },
    data() {
        return {
            /*当前专题名称*/
            specialName: "专题列表",
            /*加载更多配置*/
            loadingListOptions: {
                /*是否在加载*/
                loading: false,
                /*是否已经结束*/
                finished: false,
                /*列表*/
                list: [],
                /*当前页数*/
                current: 1,
                /*总页数*/
                totalPage: 0,
                /*总条数*/
                totalCount: 0,
                /*每页条数*/
                pageSize: 12,
                /*第一次加载动画*/
                firstLoadingDone: false
            },

        }
    },
    created() {
        this.getList(0);
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
/*精选专题*/
.specialListStyle {
    width: 100%;
    height: auto;
    overflow: hidden;
    box-sizing: border-box;
    /*display: flex;*/
    /*justify-content: space-between;*/
}

.specialListStyle > div.img {
    width: 100%;
    height: auto;
    overflow: hidden;
    /*flex-shrink: 0;*/
    box-sizing: border-box;
    margin-bottom: 10px;
}
.specialListStyle > div.text {
    width: 100%;
    height: auto;
    overflow: hidden;
    box-sizing: border-box;
    /*padding-left: 10px;*/
}
.specialListStyle > div.text .top{
    color: #EAF1FF;
    font-size: 14px;
}
.specialListStyle > div.text .bottom{
    color: #A0A4AE;
    font-size: 14px;
}
.specialListStyle > div.img img {
    display: block;
    width: 100%;
}

.specialListBox {
    width: 100%;
    height: auto;
    overflow: hidden;
    /*display: flex;*/
    box-sizing: border-box;
    /*flex-wrap: wrap;
    padding: 5px 5px;*/
}

.specialListBox > li {
    width: 100%;
    box-sizing: border-box;
    margin-top: 10px;
}
.specialListBox > li:first-child{
    margin-top: 0;
}
</style>



