<template>
    <div class="publicAllContentBg fullHeight" id="actorList">
        <!--顶部tabbar-->
        <van-nav-bar
            title="演员列表"
            class="publicAllNavbarClass"
            :border="false"
            left-arrow
            @click-left="$router.go(-1)"
        />
        <publicLoadingAnm v-if="!loadingListOptions.firstLoadingDone"></publicLoadingAnm>
        <noResult v-if="loadingListOptions.list.length===0&&loadingListOptions.firstLoadingDone" desc="暂无歌手"></noResult>
        <!--视频列表-->
        <van-list
            v-if="loadingListOptions.list.length>0&&loadingListOptions.firstLoadingDone"
            v-model="loadingListOptions.loading"
            :finished="loadingListOptions.finished"
            @load="onLoad"
            :immediate-check="false"
        >
            <!--歌手列表-->
            <ul class="list">
                <li v-for="(item,index) in loadingListOptions.list" :key="index">
                    <div class="listStyle"  @click="$router.push({name:'actorContent',params:{id:item.id}})">
                        <!--<van-image
                            width="50px"
                            height="50px"
                            fit="fill"
                            round
                            :src="item.headIco"
                        />-->
                        <aesImage :primordial-img="false" :file-url="item.headIco"></aesImage>
                        <p>{{item.name}}</p>
                    </div>
                </li>
            </ul>
            <!--<van-cell
                style="padding:10px"
                v-for="(item,index) in loadingListOptions.list" :key="index"
                :title="item.name"
                :label="item.description"
                @click="$router.push({name:'artistIndex',params:{id:item.id}})"
            >
                <template #icon>
                    <van-image width="50" height="50" round :src="item.headIco"/>
                </template>
            </van-cell>-->
        </van-list>
    </div>
</template>

<script>
import noResult from "../../components/noResult";
import publicLoadingAnm from "../../components/publicLoadingAnm";
import aesImage from "../../components/aesImage";
export default {
    name: 'actorList',
    components:{
        aesImage,
        publicLoadingAnm,
        noResult
    },
    methods:{
        /*获取列表*/
        getList(type){
            this.loadingListOptions.firstLoadingDone=false;
            this.$requestApi.getArtistList({
                length:this.loadingListOptions.pageSize,
                page:type===1?this.loadingListOptions.current+1:1,
                type:2
            })
                .then(res=>{
                    if(res.data.list.length===0){
                        return;
                    }
                    if(type===0){
                        this.loadingListOptions.list=res.data.list;
                    }
                    else{
                        this.loadingListOptions.list=this.loadingListOptions.list.concat(res.data.list);
                    }
                    /*放置总页数*/
                    this.loadingListOptions.totalPage=this.$globalFn.totalPageCountFn(this.loadingListOptions.pageSize,res.data.recordsTotal);
                    /*放置当前页数*/
                    this.loadingListOptions.current=res.data.page;
                    /*记录总条数*/
                    this.loadingListOptions.totalCount=res.data.recordsTotal;
                    console.log("打印当前页数");
                    console.log(this.loadingListOptions.current);
                    console.log("打印当前总页数");
                    console.log(this.loadingListOptions.totalPage);
                    /*判断是否已经到最后一页，如果是，那么就停止下拉加载*/
                    if(this.loadingListOptions.totalPage===this.loadingListOptions.current){
                        this.loadingListOptions.finished=true;
                    }
                    else if(this.loadingListOptions.totalPage===1){
                        this.loadingListOptions.finished=true;
                    }
                    /*关闭加载动画*/
                    this.loadingListOptions.loading=false;
                    this.loadingListOptions.firstLoadingDone=true;
                })
                .catch(err=>{

                })
        },
        /*继续加载*/
        onLoad(){
            console.log("触底加载");
            this.getList(1);
        }
    },
    data() {
        return {
            /*当前专题名称*/
            specialName:"歌手列表",
            /*加载更多配置*/
            loadingListOptions:{
                /*是否在加载*/
                loading:false,
                /*是否已经结束*/
                finished:false,
                /*列表*/
                list:[],
                /*当前页数*/
                current:1,
                /*总页数*/
                totalPage:0,
                /*总条数*/
                totalCount:0,
                /*每页条数*/
                pageSize:100,
                /*第一次加载*/
                firstLoadingDone:false
            },

        }
    },
    created() {
        this.getList(0);
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.listStyle{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}
.listStyle>p{
    font-size: 14px;
    color: #EAF1FF;
    text-align: center;
    width: 100%;
    margin-top: 4px;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
}
.list{
    box-sizing: border-box;
    padding:5px;
    display: flex;
    flex-wrap: wrap;
}
.list>li{
    box-sizing: border-box;
    padding:14px;
    width: 25%;
}
</style>



