import Vue from 'vue';
import vuex from 'vuex'
import VuexPersistence from 'vuex-persist'
Vue.use(vuex);

const vuexLocal = new VuexPersistence({
    storage: window.localStorage,
    key:'videoMobileDarkStore'
});
export default new vuex.Store({
    strict: true,
    state:{
        /*是否登录*/
        isLogin:false,
        /*当前是开发还是正式(0开发 1测试 2正式 )*/
        mode:2,
        /*用户信息(一整个对象)*/
        userInfo:null,
        /*是否显示底部栏*/
        showTabbar:true,
        /*历史搜索记录*/
        searchHistory:[],
        /*当前用户机器码*/
        imeiCode:null,
        /*用户名*/
        userName:null,
        /*密码*/
        password:null,
        /*当前搜索类型存储*/
        searchInfoSave:{
            /*type:
            * 1.info就是关键字 文本匹配： 视频名称、视频标签、视频分类
            * 2.id 类型info传递id
            * 3.videoType:1-所有可用 2-免费 3-精选 4-G点视频(收费视频) 默认搜索全部
            * 4.sortFn:排序方式 如果搜索结果页面接收到的是null，那么就直接按照搜索页面上的排序方法，否则按照存储的
            * */
            type:1,
            info:"",
            videoType:1,
            sortFn:null
        },
        /*支付宝账号*/
        alipayAccount:null,
        /*支付宝真实姓名*/
        alipayName:null,
        bankAccount:null,
        /*当前提现金额*/
        nowGetCashCount:0,
        /*当前代理id*/
        agentId:null,
        /*常用参数配置存储*/
        configInfo:null,
        /*浏览历史保存数组*/
        videoHistoryList:[],
        /*分享链接*/
        shareList:[],
        /*广告位信息*/
        adList:[],
        /*下载信息*/
        downloadUrl:{
            androidDownUrl:null,
            IOSDownUrl:null
        },
        /*记录滚动位置*/
        scrollPosition:0
    },
    getters:{

    },
    mutations:{
        /*改变滚动位置的方法*/
        changeScrollPos(state,val){
            state.scrollPosition=val;
        },
        /*改变下载信息的方法*/
        changeDownloadUrl(state,obj){
            state.downloadUrl.androidDownUrl=obj.androidDownUrl;
            state.downloadUrl.IOSDownUrl=obj.IOSDownUrl;
        },
        /*增加广告位信息的方法*/
        changeAdList(state,val){
            state.adList=val;
        },
        /*存储分享链接*/
        saveShareList(state,val){
            state.shareList=val;
        },
        /*操纵浏览历史的方法*/
        changeVideoHistory(state,val){
            state.videoHistoryList=val;
        },
        /*存储常用参数*/
        configInfoSave(state,val){
            state.configInfo=val;
        },
        /*改变用户账号密码的方法*/
        changeUserAccount(state,val){
            state.userName=val.userName;
            state.password=val.password;
        },
        /*改变当前机器码*/
        changeImeiCode(state,val){
            state.imeiCode=val;
        },
        /*当前代理id记录*/
        saveAgentID(state,val){
            state.agentId=val;
        },
        /*添加历史记录的方法*/
        changeSearchHistory(state,obj){
            /*
            * obj:{
            *   type:1  1:添加元素 0.清空
            *   val:""   搜索关键字
            * }
            * */
            if(obj.type===1&&obj.val===""){
                return;
            }
            if(obj.type===1){
                state.searchHistory.indexOf(obj.val)===-1?state.searchHistory.push(obj.val):null;
            }
            else if(obj.type===0){
                state.searchHistory=[];
            }

        },
        /*改变用户信息里某一个属性值的方法*/
        changeUserInfoOneKey(state,obj){
            /*
            * obj:{key:'',val:''}
            * */
            state.userInfo[obj.key]=obj.val;
        },
        /*改变银行账号的方法*/
        changeBankAccount(state,val){
            state.bankAccount=val;
        },
        /*改变传递信息的方法*/
        changeSearchInfo(state,val){
            state.searchInfoSave=val;
        },
        /*临时关闭底部栏的方法*/
        changeBottomTabbar(state,val){
            state.showTabbar=val;
        },
        /*改变登录状态*/
        changeLoginStatus(state,val){
            state.isLogin=val;
        },
        /*用户信息存储*/
        userInfoSave(state,val){
            state.userInfo=val;
        },
        /*单独更改用户token的方法*/
        changeUserToken(state,val){
            state.userInfo.token=val;
        }
    },
    actions:{

    },
    plugins: [vuexLocal.plugin]
});



